<template>
    <div>
        <dialogs type="customer" ref="addPricePlan" v-if="showDialog" width="45em" :hasNoPaddB='true' overflow="auto">
            <div slot="header">
                {{operateFlag == 0 ? '添加项目' : '编辑项目'}}
            </div>
            <div slot="content">
                <ul class="dialog-ul">
                    <li v-for="(item,$index) in addList" class=" formList " :class="item.classes">
                        <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span>
                            {{item.label}} </label>
                        <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span>&nbsp;</span>{{item.label}}
                        </label>
                        <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty" :option="item.options"
                                @getInput="getFrom" :ref="item.options.key"></inputs>
                        <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom"
                                 :ref="item.options.key"></selects>
                    </li>
                </ul>
            </div>
            <div slot="footer">
                <button class="button-default" @click="submitData">提交</button>
                <button class="button-default cancel-button" @click="cancelDialog">取消</button>
            </div>
        </dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                showDialog: false,
                formData: {
                    companyName: '',
                    projectAddr: '',
                    projectName: '',
                    basePrice: '',
                    upPrice: '',
                    communityName: '',
                    contact: '',
                    contactTelphone: '',
                    remark: '',
                    orgNo: ''
                },
            }
        },
        props: ['priceData', 'operateFlag'],
        computed: {
            addList: function () {
                let width = 'calc(100% - 6.5em)';
                return [
                    {
                        classes: 'allWidth',
                        type: 'input',
                        isEmpty: false,
                        label: '项目名称',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.projectName,
                            length: 50,
                            key: 'formData.projectName'
                        }
                    },

                    {
                        classes: 'halfWidth ',
                        type: 'input',
                        isEmpty: false,
                        label: '基础电价',
                        options: {
                            type: 'normalInput',
                            width: width,
                            placeHolder: '大于0且小于99.99，允保留两位小数',
                            errorMsg: '基础电价： 大于0且小于99.99，允保留两位小数',
                            length: 5,
                            dataType: '+float',
                            limit: 'float',
                            // regular: 'float4',
                            key: 'formData.basePrice',
                            value: this.formData.basePrice
                        }
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: false,
                        label: '提成电价',
                        options: {
                            type: 'normalInput',
                            width: width,
                            placeHolder: '0-99.99之间，允保留两位小数',
                            value: this.formData.upPrice || 0,
                            length: 5,
                            //dataType: '+float',
                            limit: 'float',
                            key: 'formData.upPrice'
                        }
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        isEmpty: false,
                        label: '小区名称',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.communityName,
                            length: 100,
                            key: 'formData.communityName'
                        }
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: true,
                        label: '物业名称',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.companyName,
                            length: 200,
                            key: 'formData.companyName'
                        }
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        isEmpty: true,
                        label: '联系人员',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.contact,
                            length: 20,
                            key: 'formData.contact'
                        }
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: true,
                        label: '联系电话',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.contactTelphone,
                            length: 11,
                            regular: 'phone',
                            errorMsg: '联系电话： 11位手机号码',
                            key: 'formData.contactTelphone'
                        }
                    },
                    {
                        classes: 'halfWidth ',
                        type: 'select',
                        isEmpty: false,
                        label: '组织机构',
                        options: {
                           type: 'complexSelect',
                           key: 'formData.orgNo',
                           cs: 'server',
                           url: 'org/queryOrgTreeByOrgNo',
                           method: 'post',
                           noCheck: {key: 'leaf', value: 0}, 
                           constant: this.formData.orgNo != '' ? {orgNo: this.formData.orgNo}: '' ,
                           width: width,
                           text: 'orgName',
                           value: 'orgNo',
                           rowName: 'data'
                       }
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: false,
                        label: '小区地址',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.projectAddr,
                            length: 200,
                            key: 'formData.projectAddr'
                        }
                    },
                    {
                        classes: 'allWidth',
                        type: 'input',
                        isEmpty: true,
                        label: '备注信息',
                        options: {
                            type: 'normalInput',
                            value: this.formData.remark,
                            width: width,
                            placeHolder: '50个字符内',
                            key: 'formData.remark'
                        }
                    }
                ]
            }
        },
        methods: {
            cancelDialog: function () {
                this.showDialog = false;
                this.formData = this.cleanSearchData(this.formData);
                this.monthly = this.cleanSearchData(this.monthly);
                this.temporary = [];
                this.$refs.addPricePlan.close()
            },
            showOperate: function () {
                this.copyData();
                this.showDialog = true;
                let that = this;
                setTimeout(function () {
                    that.$refs.addPricePlan.show()
                }, 0)
            },
            getFrom: function (val) {
                this.getOption(val, true);
            },
            submitData: function () {
                let that = this;
                let alerts = that.$refs.alerts;
                that.regularTest({
                    emptyKey: that.showItem == '2' ? [] : ['projectName', 'projectAddr', 'basePrice', 'upPrice', 'communityName'],
                    prefix: 'formData',
                    callback: function () {
                        if (parseFloat(that.formData.basePrice) == 0 || parseFloat(that.formData.basePrice) >= 100) {
                            alerts.show({msg: '基础电价应大于0且小于100 !'});
                            return
                        }
                        let param = that.formData;
                        // that.$emit('priceData', that.getSearchData(param));
                        that.$emit('priceData', param);
                        that.cancelDialog();
                    }
                })
            },
            copyData: function () {
                if (this.operateFlag == 1) {
                    for (let key in this.formData) {
                        this.formData[key] = this.priceData[key] || ''
                    }
                }
            },
        },
        created: function () {
            this.copyData()
        },
        watch: {
            priceData(val, oldVal) {

            }
        }
    }
</script>
<style lang="scss" scoped>
    .tab-div-box {
        width: 100%;
        ul {
            width: 100%;
            display: flex;
            justify-content: space-around;
        }
        li {
            width: 33%;
            float: left;
            cursor: pointer;
            text-indent: 2px;
            height: 3em;
        }
        .selectedLi{
            border: 1px solid rgba(255, 255, 255, 0.15);
            border-bottom: 1px solid transparent;
            position: relative;
            z-index: 1;
            background: rgba(3,23,68,1);
        }
    }
    .recharge-Config{
        position: relative;
        top: -1px;
        border: 1px solid rgba(255, 255, 255, 0.15)
    }
</style>


