<template>
	<div ref="trys" style="height: 100%">
	    <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></info>
      </div>
   	  <div  v-show="!showInfo"  class="all-container" :class="{priceInfo: !!project}">
   	  
        <div class="operate-div operateBtn">
            <button class="button-operate" @click="addIC" v-if="controls[1]">
                   <i class="hasIcon hasImg">
                      <icon name="plus-circle" scale="1.4"></icon>
                   </i>添加
            </button>
            <button class="button-operate" @click="operateICCard(1)" v-if="controls[3]">
                   <i class="hasIcon hasImg">
                       <icon name="trash" scale="1.4"></icon>
                   </i>删除
            </button>
            <button class="button-operate" @click="operateICCard(2)" v-if="controls[40]">
                   <i class="hasIcon hasImg">
                       <icon name="ban" scale="1.4"></icon>
                   </i>停用
            </button>
             <button class="button-operate" @click="operateICCard(3)" v-if="controls[41]">
                   <i class="hasIcon hasImg">
                       <icon name="plug" scale="1.4"></icon>
                   </i>启用
            </button>
            <!--<button class="button-operate" @click="exportCardList" v-if="controls[9] || true">-->
                   <!--<i class="hasIcon hasImg">-->
                         <!--<img src="dist/img/file/reload-file-buttton.png" />-->
                   <!--</i>导入-->
            <!--</button>-->
           
        </div>
   	  	<div class="operate-div">
            <template v-for="(item, $index) in formList">
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
                <selects ref="selects" v-if="item.type == 'select'" :option="item.options" @getSelect="getOption"></selects>
            </template>
         </div>
         <div ref="myOwnTable" class="table-container" >
            <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"
                    @rowDbClick="checkInfo"></tables>
         </div>
   	  </div>
   	  <dialogs type="customer" ref="addICCard" v-if="showDialog" width="45em" :hasNoPaddB='true'>
        <div slot="header">
          添加IC卡
        </div>
        <div slot="content" >
          <ul class="dialog-ul">
            <li v-for="(item,$index) in addList" class=" formList " :class="item.classes">
              <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
              <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
              <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
              <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
            </li>
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitDevice">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="loading" ref="loadings"></dialogs>
      <loginAgain ref="loginAgain" @nextOperate="nextOperate" tipMsg="请输入当前用户密码以验证身份，回车提交数据" @closeOperate="getParams()"></loginAgain>
      <fileInput ref="uploadTarget" :upLoadAttrParam="upLoadFile.upLoadAttrite"
               :ForthwithLoadParam="upLoadFile.ForthwithLoad" @upLoadFileFunc="analyzeExcel"></fileInput>
      <dialogs type="customer" width="30em" ref="resolute" v-if="showDialogs">
         <div slot="header">
            导入结果
         </div>
         <div slot="content">
           <p style="font-size: 16px;text-align: center;line-height: 40px "> IC卡资料成功导入<b>
              {{importInfo.successCount}} </b>条， 失败 <b>{{importInfo.failCount}} </b>条；</p>
          </div>
          <div slot="footer">
             <button  v-if="importInfo.failCount>0" @click="importFailExcel"
                 class="button-default"
                 data-dismiss="modal">下载失败资料
             </button>
             <button class="button-default" @click="closeDialog">关闭</button>
          </div>
      </dialogs>         
   </div>
</template>

<script>
   import 'vue-awesome/icons/plus-circle'
   import 'vue-awesome/icons/plug'
   import 'vue-awesome/icons/ban'
   import info from './info.vue'

   import loginAgain from '../../childComponent/userValidate.vue'
   import fileInput from '../../childComponent/UploadFile.vue'
   export default{
     data() {
        return {
         showInfo: false,
         selected: [],
         showDialog: true,
         searchParam: {
            cardId:'',
            cardState: ''
         },
         currentOpeFlag: '', //1代表删除，2代表停用，3代表启用
         row: '',
         controls: [],
         tableOpt: {
            column: [
              {field: 'state',type: 'checkBox', textAlign: 'center',  width: '30'},
              {title: '序号', field: '', textAlign: 'center', width: '100', type: 'string', formatter: function (val, index, row, obj, indexs, startNum) {
                  return indexs + startNum;
                }
              },
              {title: '状态', field: 'cardState', sortable: true, textAlign: 'center', width: '50', type: 'string', formatter: function (val, index, row, obj) {
                  let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                  switch(vals) {
                    case '0':
                      return '<div>初始</div>'
                    case '1':
                      return '<div class="green">启用</div>'
                    case '2':
                       return '<div class="red">停用</div>'
                    default:
                      return '<div >--</div>'
                  }
              }},
              {title: '卡号', field: 'cardId',textAlign: 'center',width: '130'},
              {title: '微信ID', field: 'webcharNo',textAlign: 'center',width: '220'},
              {title: '支付宝ID', field: 'alipayUserId',textAlign: 'center',width: '130'},
              {title: '剩余金额', field: 'icRemainAmount',  sortable: true,  textAlign: 'center', width: '100'},
              {title: '使用次数', field: 'useCnt',  sortable: true,  textAlign: 'center', width: '100'},
              {title: '充值次数', field: 'payCnt',  sortable: true,  textAlign: 'center', width: '100'},
              {title: '创建时间', field: 'createTime', textAlign: 'center', width: '160'},
              {title: '更新时间', field: 'updateTime',  sortable: true,  textAlign: 'center', width: '160'},
              {title: '备注', field: 'remark', textAlign: 'center', width: '250'}
            ],
            url: 'chargerIC/queryList',
            method: 'post',
            // method: 'get',
            // url: 'dist/json/ICMana.json',
            pageSize: 20,
            pageNumber: 1,
            pageList: [20, 40, 60],
            queryParam: {},
            isExport: true,
            sortName: 'updateTime',
            sortOrder: 'desc',
            exportURL: 'chargerIC/downloadList'
         },
         tableShow: false,
         formData: {
           remark: '',
           cardId: '',
           customerName: '',
           initAmount: '',
           customerContact: '',
           projectGuid: '',
           cardGuid: ''
        },
        upLoadFile: {
            //即时上传
            ForthwithLoad: true,
            //上传属性
            upLoadAttrite: {
                 url: 'chargerIC/uploadFile',
                 extensions: ['xls', 'xlsx'], //上传文件的后缀
                 fileSize: 30000,//文件的大小10M
                 templateUrl: 'dist/modal/用户档案模板.xls' //模板路径
            }
         },
         showDialogs: false,
         importInfo: { //导入档案
              importId: '',
              successCount: 0,
              failCount: 0,
          },
       }
     },
     components: {
         info,
         loginAgain,
         fileInput
    },
       props: ['table', 'search', 'project'],
     computed: {
     	formList: function () {
     	  	let arr = this.getEnumItem('cardStatus').obj;
          arr.unshift({text:'全部', value: ''});
            return [
              
              {
                type: 'select',
                options: {
                  cs: 'client',
                  width: '7.5em',
                  data: arr,
                  text: 'text',
                  value: 'value',
                  key: 'searchParam.cardState'
                }
              },
               {
                 type: 'input',
                 options: {
                   type: 'typeSearch',
                   key: 'searchParam',
                   selectOpt: {
                     width: '7em',
                     cs: 'client',
                     data: [
                      {text: '编号', value: 'cardId'}
                     ]
                   }
                 }
               }
            ]
     	},
     	addList: function () {
     		let width = 'calc(100% - 6.5em)';
     		let obj = [
                {  
                	classes: 'halfWidth',
                	type: 'input', 
                	isEmpty: false, 
                	label: '卡号',
                	options: {
                		type: 'normalInput', 
                		width: width,  
                		limit: 'number',
                		placeHolder: '1-8位数字',
                        value: this.formData.cardId,
                		length: 8,
                		key: 'formData.cardId'
                    }
                },
                {
                    classes: 'halfWidth hasMarginLi',
                    type: 'input',
                    isEmpty: false,
                    label: 'IC卡UID',
                    options: {
                        key: 'formData.cardGuid',
                        width: width,
                        type: 'normalInput',
                        length: 16,
                        value: this.formData.cardGuid,
                        placeHolder: '1-16位数字和字母（a-f、A-F）组合',
                        errorMsg: '1-16位数字和字母（a-f、A-F）组合',
                        limit: 'moteEUI',
                        regular:  'otherDeviceNo' ,
                    }
                },
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '初始金额', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    placeHolder: '0-500之间的任意整数', 
                    length: 3,
                    limit: 'number',
                    key: 'formData.initAmount',
                    value: this.formData.initAmount
                  }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: true, 
                  label: '姓名', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    placeHolder: '1-20个字符', 
                    value: this.formData.customerName,
                    length: 20,
                    key: 'formData.customerName'
                    }
                }]
            if(Array.isArray(this.project) && this.project.length > 0){
     		    obj.push({
                    type: 'select',
                    classes: 'halfWidth',
                    label: '所属项目',
                    isEmpty: false,
                    options: {
                        width: width,
                        type: 'select',
                        cs: 'client',
                        data: [{value: this.project[1], text: this.project[0]}],
                        key: 'formData.projectGuid'
                    }
                })
            }else{
     		    obj.push({
                    type: 'select',
                    classes: 'halfWidth',
                    label: '所属项目',
                    isEmpty: false,
                    options: {
                        type: 'complexSelect',
                        key: 'formData.projectGuid',
                        cs: 'server',
                        url: 'charger/queryProject',
                        method: 'post',
                        //noCheck: {key: 'leaf', value: 0},
                        width: width,
                        text: 'projectName',
                        value: 'projectGuid'
                    }
                })
            }
            obj.push({
                classes: 'halfWidth hasMarginLi',
                type: 'input',
                isEmpty: false,
                label: '电话',
                options: {
                    key: 'formData.customerContact',
                    width: width,
                    type: 'normalInput',
                    length: 11,
                    value: this.formData.customerContact,
                    regular: 'phone',
                    errorMsg: '电话：11位手机号',
                    limit: 'number'
                }
            })
            obj.push({
                classes: 'allWidth',
                type: 'input',
                isEmpty: true,
                label: '备注信息',
                options: {
                    type: 'normalInput',
                    value: this.formData.remark,
                    width: width,
                    placeHolder: '50个字符内',
                    key: 'formData.remark'
                }
            })
            return obj;
     	}
     },
  
     methods: {
         copyData: function () {
             this.tableOpt.column = this.table;
             this.searchParam = this.search;
             this.tableOpt.queryParam = this.searchParam
             this.$set(this.formData, 'projectGuid', this.project[1])
         },
         closeDialog: function () {
                this.showDialog = false;
                this.$refs.resolute.close();
         },
         analyzeExcel: function () {
                let that = this;
                let reqpara = {
                    saveName: that.$refs.uploadTarget.upLoadedData.saveName,
                    fileName: that.$refs.uploadTarget.upLoadedData.fileName
                };
                that.$refs.loadings.show({
                    msg: '正在解析excel文件,请稍后...'
                });
                //提交解析数据
                that.getRequest({
                    url: 'customerInfo/batchSave',
                    method: 'post',
                    param: reqpara,
                    success: function (data) {
                        that.$refs.loadings.close();
                        let Data = data.data;
                        that.importInfo.importId = that.$refs.uploadTarget.upLoadedData.importId;
                        that.importInfo.successCount = Data.successCount;
                        that.importInfo.failCount = Data.failCount;
                        that.resultDialog();
                    },
                    error: function (data) {
                       that.$refs.loadings.close();
                       that.alerts.show({
                         msg: data.message
                       });
                   }
                }, true);
         },
         //显示上传结构弹出框
        resultDialog: function () {
            let that = this;
            that.showDialogs = true;
            setTimeout(function () {
                that.$refs.resolute.show();
            }, 0)
        },
        importFailExcel: function () {
            if (this.importInfo.failCount > 0) {
                let reqpara = {
                     importId: this.importInfo.importId,
                     isDown: 1
                };
                this.downLoad({url: 'customerInfo/downloadErrorData', data: reqpara})
            }
        },
        exportCardList: function () {
          this.$refs.uploadTarget.show();
        },
        nextOperate: function () {
          switch(this.currentOpeFlag) {
            case 1:
              this.deleteIC();
              break;
            case 2:
              this.stopOrOpenIC(0);
              break;
            case 3:
              this.stopOrOpenIC(1);
              break;    
          }
        },
     	  checkInfo: function (val) {
          this.row = val.val;
              this.row = val.val;
              if(Array.isArray(this.project) && this.project.length > 0){
                  this.$parent.setInfo(val.val);
              }else{
                  this.showInfo = true;
              }
       	},
     	  closeDiv: function () {
            this.showInfo = false;
            this.getParams();
        },
        cancelDialog: function ()  {
            this.showDialog = false;
            this.formData = this.cleanSearchData(this.formData);
            this.$refs.addICCard.close()
        },
     	 //添加
        addIC: function () {
            this.showDialog = true;
            let that = this;
            setTimeout(function () {
            	that.$refs.addICCard.show()
            }, 0)
        },
        removeIC: function () {
            let that = this;
            this.getConfirm({
            	emptyMsg: '请先选择要删除的IC卡!',
            	confirmMsg: '确定要对选中的IC卡进行删除操作吗？',
            	callback: {
            		confirm: function () {
                  that.currentOpeFlag = 1;
                  that.$refs.loginAgain.show();
            			//that.deleteDevice();
            		}
            	}
            });
        },
        operateICCard: function(flag) {
            let that = this;
            let operateText = '';
            switch(flag) {
               case 1: 
                  operateText = '删除';
                  break;
               case 2: 
                  operateText = '停用';
                  break;
               case 3: 
                  operateText = '启用';
                  break;
            }
            that.getConfirm({
              emptyMsg: `请先选择要${operateText}的IC卡!`,
              confirmMsg: `确定要对选中的IC卡进行${operateText}操作吗？`,
              callback: {
                confirm: function () {
                  that.currentOpeFlag = flag;
                  that.$refs.loginAgain.show();
                }
              }
            });
        },
        submitDevice: function () {
           let that = this;
            if (this.formData.cardId.length < 8) {
                this.formData.cardId = this.padLeft(this.formData.cardId, 8);
            }
           that.regularTest({  
              emptyKey: ['cardId', 'customerContact', 'initAmount', 'cardGuid'],
              prefix: 'formData',
              callback: function () {
                  if(that.formData.projectGuid == '') {
                      that.$refs.alerts.show({
                          msg: '所属项目不能为空！'
                       });
                      return 
                  }
                  if(parseInt(that.formData.initAmount) > 500) {
                      that.$refs.alerts.show({
                          msg: '初始金额不能大于500元！'
                       });
                      return 
                  }
                  let reg = /^[a-zA-Z0-9]+$/g;
                  if(that.formData.cardGuid.length < 16 || !reg.test(that.formData.cardGuid)){
                      that.$refs.alerts.show({
                          msg: 'IC卡UID需为16位字符或者数字！'
                      });
                      return
                  }
                  that.$refs.loadings.show({msg: '正在提交数据，请稍后。。。', hasClose: true});
                  that.getRequest({
                    url: 'chargerIC/add',
                    method: 'post',
                    param: that.formData,
                    success: function (data) {
                       that.$refs.loadings.close();
                       that.$refs.alerts.show({
                          msg: data.message
                       });
                       that.formData = that.cleanSearchData(that.formData, ['projectGuid']);
                       that.getParams(1);
                    },
                    error: function (data) {
                       that.$refs.loadings.close();
                       that.$refs.alerts.show({
                          msg: data.message
                       });
                    }
                  })
              }
            })
        },
        deleteIC: function () {
            let that = this;
            let alerts = that.$refs.alerts;
            let arr = [];
            for(let i =0, l= that.selected.length; i<l; i++) {
               let id = that.selected[i]['cardId'];
               // if(parseInt(that.selected[i]['icRemainAmount']) > 0 ){
               //    alerts.show({msg: `IC卡 (${id}) 余额不为0，不能删除，请重新选择！`});
               //    return 
               // }
               arr.push(id);
            }
            this.deleteData({
            	url: 'chargerIC/delete',
            	params: ['cardId'],
            	callback: function (data) {
                // alerts.show({ msg: data.message});
                 that.getParams(1);
            	},
            	error: function (data) {
                    that.getParams(1);
            	}
            })
        },
        stopOrOpenIC: function (flag) {
            let that = this;
            let arr = [];
            for(let i =0, l=that.selected.length; i<l; i++) {
               arr.push(that.selected[i]['cardId']);
            }
            let param = {
              cardIds: arr,
              onOrOff: flag
            };
            that.getRequest({
              url: 'chargerIC/offOnCard',
              param: param,
              method: 'post',
              success: function (data) {
                 that.$refs.alerts.show({
                    msg: data.message
                 });
                that.getParams();
              },
              error: function (data) {
                 that.$refs.alerts.show({
                    msg: data.message
                 });
              }
            })
        },
       
       	getFrom: function (val) {
     	    if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['cardState', 'projectGuid']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
       	},
       	getParams: function (pageNumber) {
          let arr = this.getSearchData(this.searchParam);
          this.getHeight();
          this.searchTable(arr, pageNumber);
       	},
       	resizeCon: function () {
          this.getHeight();
          if(this.$refs.infoDetail) {
            let info = this.$refs.infoDetail;
            if(info.$refs.myTable) {
               info.$refs.myTable.onResize()
            }
          }
       	},
       	windowChange: function () {
          this.resizeCon();
          // let that = this;
          // window.onresize = function () {
          //   that.resizeCon();
          // }
        },
        init: function () {
          let that = this;
        //  this.tableOpt.isExport = this.controls[6];
          this.tableShow = true;
          // window.onresize = function () {
          //    that.windowChange()
          // }
        }
    },
     created: function () {
         this.isIn();
         this.getAction();

     },
     beforeRouteEnter(to, from, next) {
        next(vm => { 
           
        });
     },
     watch: {
        'searchParam.cardState'(val, oldVal) {
            this.getParams(1)
        }
     }
    }
</script>

<style lang="scss">
    .priceInfo{
        background: rgba(53, 136, 250, 0.0) !important;
        box-shadow:none !important;
        padding: 0 !important;
        width: 100% !important;
    }
  .dialog-ul .hasCheck-allWidth {
    width: fit-content!important;
    width:  -webkit-fit-content!important;
    width:  -moz-fit-content!important;
    width:max-content;
    float: left;
    .inputChoose{
        line-height: 2.5;
        padding-left: 1em;
    }
    input.checkBox{
    	left:0.7em
    }
  }
</style>


