<template>
    <div ref="trys" style="height: 100%">
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></info>
        </div>
        <div v-show="!showInfo" class="all-container noback-Container">
            <searchTree  @getSingleNode="getSelectNode" :noCustomer="true" :singleSelect="true" :noSearch="true" :noLastChild="true"></searchTree>
            <div class="TreeRight TreeRightes">
              <div class="operate-div operateBtn">
                <button class="button-operate" @click="addPlan" v-if="controls[1]">
                    <i class="hasIcon hasImg">
                        <icon name="plus-circle" scale="1.4"></icon>
                    </i>添加
                </button>
              </div>
              <div class="operate-div">
                <template v-for="(item, $index) in formList">
                    <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"
                            @getInput="getFrom"></inputs>
                    <selects ref="selects" v-if="item.type == 'select'" :option="item.options"
                             @getSelect="getOption"></selects>
                </template>
              </div>
              <div ref="myOwnTable" class="table-container">
                  <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"
                          @rowDbClick="checkInfo"></tables>
              </div>
            </div>
        </div>

        <dialogs type="confirm" ref="conformDialog"></dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
        <operateDia @priceData="submitPrice" ref="addPricePlan" operateFlag="0"></operateDia>
    </div>
</template>

<script>
    import 'vue-awesome/icons/plus-circle'
    import 'vue-awesome/icons/ban'
    import info from './info.vue'
    import operateDia from './addOrEdit.vue'
    import searchTree from '../child/leftSearchMenu.vue'
    export default {
        data() {
            return {
                showInfo: false,
                selected: [],
                showDialog: true,
                searchParam: {
                    projectName: '',
                    nodeType: '',
                    nodeId: ''
                },
                currentOpeFlag: '', //1代表删除，2代表停用，3代表启用
                row: '',
                controls: [],
                tableOpt: {
                    column: [
                        
                        {title: '站点名称', field: 'projectName', textAlign: 'center', width: '200'},
                        {title: '设备数量', field: 'deviceNum', sortable: false, textAlign: 'center', width: '140'},
                        {title: '小区名称', field: 'communityName', sortable: false, textAlign: 'center', width: '140'},
                        {title: '组织机构', field: 'orgName', sortable: false, textAlign: 'center', width: '120'},
                        {title: '物业公司', field: 'companyName', sortable: false, textAlign: 'center', width: '140'},
                        {title: '创建时间', field: 'createTime', textAlign: 'center', width: '160'}

                    ],
                    url: 'chargerScheme/queryList',
                    method: 'post',
                    // method: 'get',
                    // url: 'dist/json/planList.json',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    isExport: true,
                    // sortName: 'createTime',
                    // sortOrder: 'desc',
                    exportURL: 'chargerScheme/downloadList'
                },
                tableShow: false,
                currentNode: '',
                showDialogs: false
            }
        },
        components: {
            info,
            operateDia,
            searchTree
        },
        computed: {
            formList: function () {
                let arr = this.getEnumItem('cardStatus').obj;
                arr.unshift({text: '全部', value: ''});
                return [

                    // {
                    //   type: 'select',
                    //   options: {
                    //     cs: 'client',
                    //     width: '7.5em',
                    //     data: arr,
                    //     text: 'text',
                    //     value: 'value',
                    //     key: 'searchParam.cardState'
                    //   }
                    // },
                    {
                        type: 'input',
                        options: {
                            type: 'typeSearch',
                            key: 'searchParam',
                            selectOpt: {
                                width: '7em',
                                cs: 'client',
                                data: [
                                    {text: '站点名称', value: 'projectName'}
                                ]
                            }
                        }
                    }
                ]
            }
        },

        methods: {
           getSelectNode: function(val) {
              this.searchParam.nodeId = val.nodeId;
              this.searchParam.nodeType = val.nodeType || 1;
              if(!this.showTable) {
                this.tableOpt.queryParam.nodeId = val.nodeId;
                this.tableOpt.queryParam.nodeType = val.nodeType
                this.showTable = true;
              } else {
                this.getParams(1);
              }
              this.currentNode = val.nodeName
            },
          
            checkInfo: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
            },

            //添加
            addPlan: function () {
                this.$refs.addPricePlan.showOperate()
            },
            removeIC: function () {
                let that = this;
                this.getConfirm({
                    emptyMsg: '请先选择要删除的方案!',
                    confirmMsg: '确定要对选中的方案进行删除操作吗？',
                    callback: {
                        confirm: function () {
                            that.deletePlan();
                        }
                    }
                });
            },

            submitPrice: function (param) {
                let that = this;
                let alerts = that.$refs.alerts;
                that.$refs.loadings.show({msg: '正在提交数据，请稍后。。。', hasClose: true});
                that.getRequest({
                    url: 'chargerScheme/add',
                    method: 'post',
                    param: param,
                    success: function (data) {
                        that.$refs.loadings.close();
                        that.$refs.alerts.show({
                            msg: data.message
                        });
                        that.getParams(1);
                    },
                    error: function (data) {
                        that.$refs.loadings.close();
                        that.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                })
            },
            deletePlan: function () {
                let that = this;
                let alerts = that.$refs.alerts;
                let arr = [];
                for (let i = 0, l = that.selected.length; i < l; i++) {
                    let id = that.selected[i]['cardId'];
                    arr.push(id);
                }
                this.deleteData({
                    url: 'chargerIC/delete',
                    params: ['cardId'],
                    callback: function (data) {
                        that.getParams(1);
                    },
                    error: function (data) {
                        that.getParams(1);
                        alerts.show({msg: data.message});
                    }
                })
            },

            getFrom: function (val) {
                if (val.combine) {
                    if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                        this.searchParam = this.cleanSearchData(this.searchParam, ['nodeId', 'nodeType']);
                        this.getOption(val, true);
                        this.getParams(1);
                    }
                } else {
                    this.getOption(val, true);
                }
            },
            getParams: function (pageNumber) {
                let arr = this.getSearchData(this.searchParam);
                this.getHeight();
                this.searchTable(arr, pageNumber);
            },
            resizeCon: function () {
                this.getHeight();
                if (this.$refs.infoDetail) {
                    let info = this.$refs.infoDetail;
                    info.pie == '' ? '' : info.pie.resize();
                    if (info.$refs.myProwTable) {
                        console.log('--info--')
                        info.$refs.myProwTable.onResize(false, this.$refs.myOwnProwTable);
                    }
                }
            },
            windowChange: function () {
                this.resizeCon();
                // let that = this;
                // window.onresize = function () {
                //   that.resizeCon();
                // }
            },
            init: function () {
                let that = this;
                this.tableOpt.isExport = this.controls[6];
                this.tableShow = true;
                // window.onresize = function () {
                //    that.windowChange()
                // }
            }
        },
        created: function () {
            this.isIn();
            this.getAction();
        },

        watch: {
            'searchParam.cardState'(val, oldVal) {
                this.getParams(1)
            }
        }
    }
</script>

<style lang="scss">
	.right-container .center-container .noback-Container{
    background: transparent;
    padding:0;
    width:100%;
  }
    .TreeRightes{
    	padding: 0 0.6em!important;
        width: calc(100% - 19.5em - 0.6em - 1.2em) !important;
       // background: transparent !important;
        margin-left:0.6em !important;
    }
    .dialog-ul .hasCheck-allWidth {
        width: fit-content !important;
        width: -webkit-fit-content !important;
        width: -moz-fit-content !important;
        width: max-content;
        float: left;
        .inputChoose {
            line-height: 2.5;
            padding-left: 1em;
        }
        input.checkBox {
            left: 0.7em
        }
    }

    .dialog-ul {
        .dialogFromCheck + .myCheckbox {
            float: left;
            top: 0.8em;
        }
        .dialogFromCheck + .myCheckbox:before {
            left: -1.5em
        }
        .halfWidth .dialogFromLB {
            margin: 0;
            float: left;
            min-width: calc(100% - 40%);
            padding: 0.4em 1.5em;
            height: 2.9em;
            line-height: 2.3;
        }
        .allWidth {
            .formLiPriceSpan {
                border: 1px solid #C17C40;
                background: #29385F;
                display: block;
                padding: 0.4em 2.6em 0.4em 1.2em;
                float: left;
                margin-right: 1em;
                margin-bottom: 0.5em;
                border-radius: 0.2em;
                position: relative;
                .removePrice {
                    display: block;
                    position: absolute;
                    right: 0.5em;
                    background: #C17C40;
                    border-radius: 0.3em;
                    width: 1.4em;
                    height: 1.4em;
                    top: 0.35em;
                    line-height: 1.4;
                    cursor: pointer
                }
            }
            input.dialogFromCheck {
                float: left
            }
        }

    }


</style>


