<template>
    <div class="info-div price-Info" style="height:100%">
        <div class="info-header ">
            <span class="callback" @click="resetComponent">充电方案配置 </span>
            <span @click="resetInfo" style="cursor: pointer"> > {{commInfo.projectName}}</span>
            <span v-if="Devicedetails"> > {{boxIndex === 1?'设备编号'+rowInfo.deviceNo: 'IC卡编号'+rowInfo.cardId}}详情</span>
        </div>
        <div v-if="Devicedetails" style="height: 100%">
            <EquipManaInfo :controls="controls" :row="rowInfo" :price="price" v-if="boxIndex === 1"></EquipManaInfo>
            <ICCardInfo :controls="controls" :row="rowInfo" :price="price" v-if="boxIndex === 2"></ICCardInfo>
        </div>
        <div class="info-container" v-if="!Devicedetails">
            <div class="list-content detail-content  list-contentB">
                <div class="list-header " style="border:0; margin-bottom: 0">
                    <i class="hasIcon hasImg">
                        <img src="dist/img/widsomApp/basic.png"/>
                    </i>基础信息
                    <button class="list-operate " @click="deletePlan" v-if="controls[3]">
                        <i class="hasIcon ">
                            <icon name="trash"></icon>
                        </i>删除
                    </button>
                    <button class="list-operate" @click="editProject" v-if="controls[2]">
                        <i class="hasIcon ">
                            <icon name="edit"></icon>
                        </i>编辑项目
                    </button>
                </div>
                <div class="lInfoCon">
                    <div class="infoL">
                        <div class="infoLCon">
                            <img src="dist/img/analyse/electric.png"/>
                            <div>
                                <span class="hasColor">{{row.deviceNum}}</span>
                                <span>设备数量</span>
                            </div>
                        </div>
                    </div>
                </div>
                <ul class="form-list rInfoCon" style="padding:0em 1em">
                    <li v-for="(item,$index) in commInfoShow" style="margin:0!important"
                        :style="{width: item.widths ? item.widths : ''}" :key="$index">
                        <span class="label-span">{{item.text}}</span>
                        <span class="label-span" style="max-width: calc(100% - 8em);" :title="item.value"> &nbsp;:&nbsp;&nbsp;{{item.value}}</span>
                    </li>
                </ul>
            </div>
            <div class="list-content detail-content  list-contentB" style="height:calc(100% - 12em)">
                <div class="price-chartCon" style="height:calc(100% - 1em);">
                    <ul class="tabBox">
                        <li v-for="(item, $index) in tabbox" :key="$index" :class="{active:boxIndex == $index}" @click="changebox(item, $index)">{{item.name}}</li>
                    </ul>
                    <div class="list-box" v-if="boxIndex === 0">
                        <div class="fanAnbox">
                            <div class="tabs-show tab-div" style="margin:0 2em 1em 0em !important">
                                <ul v-if="powerTable.length != 0">
                                    <li v-for="(tabs, $index) in powerTable" :class="{'selectedLi':  $index == index}"  style="min-width: 6em; font-size:0.94em;padding: 0.72em 1em"
                                        @click="getTabPower(tabs, $index)" :key="$index">
                                        功率{{tabs.minPower}}~{{tabs.maxPower}}W
                                    </li>
                                </ul>
                                <p v-if="powerTable.length == 0" style="margin: 10px 0">
                                    请点击右边的添加按钮，添加功率配置
                                </p>
                            </div>
                            <div class="operate-div" style="padding: 0">
                                <button class="button-operate" @click="editPlan" v-if="controls[1]||controls[2]">
                                    添加/修改
                                </button>
                            </div>
                            <div class="table-container" ref="myOwnProwTable" :class="'sColor' + index" style="height: calc(100% - 4em);border-bottom: 1px solid #677790;">
                                <tables ref="myProwTable" :tableOpt="tableProwOpt" class="tablePadding"></tables>
                            </div>
                        </div>
                        <div class="lpiechart">
                            <ul class="simpleTopTab">
                                <li v-for="(item, $index) in tabs" @click="getTab(item,$index)"
                                    :class="{selected: tabIndex == $index}" :key="$index">
                                    {{item.titles}}
                                </li>
                            </ul>
                            <div class="pieLegendDiv">
                                <div class="legendCon">
                                    <div class="legendSpanCon" v-for="(item, $index) in chartType"
                                         @click="changeType($index)" :key="$index">
                                    <span class="legendSpanBefore"
                                          :style="{background: $index != typeIndex ? 'rgba(255,255,255,0.4)' : currentColor}"></span>
                                        <span class="legendSpan">{{item.name}}</span>
                                    </div>
                                </div>
                            </div>
                            <div ref="pieChart" class="piechart"></div>
                        </div>
                    </div>
                    <div class="list-box1" v-if="boxIndex === 1">
                        <EquipMana ref="Mana" :table="tabclum[1]" :search="searchParam" :project="project"></EquipMana>

                        <!--<div class="operate-div" style="float: right">-->
                            <!--<template v-for="(item, $index) in formList">-->
                                <!--<selects ref="selects" v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom" style="float: left"></selects>-->
                                <!--<inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="float: right"></inputs>-->
                            <!--</template>-->
                        <!--</div>-->
                        <!--<div class="table-container" ref="myOwnTable1" style="height: calc(100% - 4em);border-bottom: 1px solid #677790;width: 100%">-->
                            <!--<tables ref="myTable1" :tableOpt="tableICorPrice" class="tablePadding"></tables>-->
                        <!--</div>-->
                    </div>
                    <div class="list-box1" v-if="boxIndex === 2">
                        <ICCard  ref="ICard" :table="tabclum[2]" :search="ICParam" :project="project"></ICCard>
                    </div>
                </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
        <dialogs type="customer" ref="addPricePlan" v-if="showDialog" width="60em" maxHeight="620px" :hasNoPaddB='true' overflow="auto">
            <div slot="header">方案使用记录</div>
            <div slot="content">
                <div class="table-container" style="height: calc(100% - 3em)">
                    <tables :tableOpt="tableOpt" class="tablePadding"
                            v-if="tableShow"></tables>
                </div>
            </div>
        </dialogs>
        <dialogs type="customer" ref="addICPlan" v-if="showIcDialog" width="60em" maxHeight="620px" :hasNoPaddB='true' overflow="auto">
            <div slot="header">{{boxIndex == 1?'IC卡列表': '设备列表'}}</div>
            <div slot="content">
                <div class="operate-div" style="padding: 0;float: left;margin-bottom: 10px;">
                    <button class="button-operate" @click="formatAll" v-if="boxIndex === 1">
                        <i class="hasIcon hasImg">
                            <icon name="eraser" scale="1.4"></icon>
                        </i>全部初始化
                    </button>
                    <button class="button-operate" @click="readIC">
                        <i class="hasIcon hasImg">
                            <icon name="headphones" scale="1.4"></icon>
                        </i>读取IC卡
                    </button>
                    <button class="button-operate" @click="sendIC">
                        <i class="hasIcon hasImg">
                            <icon name="arrow-circle-down" scale="1.4"></icon>
                        </i>{{boxIndex == 1?'下发IC卡':'下发设备'}}
                    </button>
                </div>
                <div class="operate-div" style="float: right;padding-top: 0;">
                    <template v-for="(item, $index) in formReadList">
                        <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="float: right"></inputs>
                    </template>
                </div>
                <div class="table-container" ref="myOwnIcTable" style="height: calc(100% - 3em)">
                    <tables ref="myTable" :tableOpt="tableIcOpt" class="tablePadding"
                            v-if="tableShow"></tables>
                </div>
            </div>
        </dialogs>
        <dialogs type="confirm" ref="conformDialog"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
        <operateDia @priceData="submitProject" ref="editProject" operateFlag="1" :priceData="commInfo"
                    showItem="1"></operateDia>
        <addEditPlan @priceData="submitPlan" ref="editPlan" operateFlag="1" :priceData="schemeList" v-if="showInfo"
                    showItem="2" :projectGuid="row.projectGuid"></addEditPlan>
    </div>
</template>
<script>
    import operateDia from './addOrEdit.vue'
    import addEditPlan from './addEditPlan.vue'
    import EquipMana from './../equipmentMana/Main.vue'
    import EquipManaInfo from './../equipmentMana/info.vue'
    import ICCard from './../ICCard/Main.vue'
    import ICCardInfo from './../ICCard/info.vue'

    import 'vue-awesome/icons/arrow-circle-down'
    import 'vue-awesome/icons/headphones'
    import 'vue-awesome/icons/eraser'
    export default {
        data() {
            return {
                price: true,
                rowInfo: '',
                Devicedetails: false,
                project:[],
                selected: '',
                date: "",
                showIcDialog: false,
                index: 0,
                boxIndex: 0,
                showDialog:false,
                schemeList: [],
                searchParam: {
                    deviceNo: '',
                    commNo: '',
                },
                ICParam: {
                    sendFlag: '',
                    cardId: ''
                },
                chartType: [
                    {name: '盈利', key: 'profitable', unit: '（元）'},
                    {name: '次数', key: 'useNum', unit: '（次）'},
                    {name: '收入', key: 'deductMoney', unit: '（元）'},
                    {name: '电量', key: 'usePower', unit: '（KWH）'},
                ],
                tabbox: [
                    {name: '方案', url:'', exportURL: ''},
                    {name: '设备', url:'charger/deviceList', exportURL: 'charger/deviceListDown'},
                    {name: 'IC卡', url:'chargerIC/queryList', exportURL: 'chargerIC/downloadList'},
                ],
                typeIndex: 0,
                currentClickPlan: '',
                showInfo: false,
                commInfo: {},
                editData: {
                    planName: '',
                    basePrice: '',
                    upPrice: '',
                    remark: ''
                },
                rangeData: {
                    overCurrent: '', overVoltage: '', underVoltage: '', underCurrent: '', underCurrentDelay: ''
                },
                ports: [
                    {
                        port: 1,
                        QR: '',
                        runkey: 'runState1',
                        overCurrent: '',
                        overVoltage: '',
                        underVoltage: '',
                        underCurrent: '',
                        underCurrentDelay: ''
                    },
                    {
                        port: 2,
                        QR: '',
                        runkey: 'runState2',
                        overCurrent: '',
                        overVoltage: '',
                        underVoltage: '',
                        underCurrent: '',
                        underCurrentDelay: ''
                    }
                ],
                currentPort: '',
                heartData: '',
                tabs: [
                    {titles: '近一周营收情况', status: 1},
                    {titles: '近一个月营收情况', status: 2},
                    {titles: '近三个月营收情况', status: 3}
                ],
                pie: '',
                tabIndex: 0,
                tableShow: false,
                tableOpt: {
                    column: [
                        {title: '端口号', field: 'port', textAlign: 'center', width: '100'},
                        {
                            title: '类别', field: 'carCategory', textAlign: 'center', width: '130', type: 'string',
                            formatter: function (val, index, item) {
                                return '电瓶车'
                            }
                        },
                        {
                            title: '计费方式', field: 'payCategory', textAlign: 'center', width: '120', type: 'string',
                            formatter: function (val, index, item, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                return obj.$parent.$parent.getEnumItem('payCategory', vals).text

                            }
                        },
                        {title: '充值费用 (元)', field: 'payMoney', textAlign: 'center', width: '130'},
                        {title: '盈利费用 (元)', field: 'profitable', textAlign: 'center', width: '130'},
                        {
                            title: '状态', field: 'state', textAlign: 'center', width: '130', type: 'string',
                            formatter: function (val) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch (vals) {
                                    case '0':
                                        return '<div class="red">正在充电</div>'
                                    case '1':
                                        return '<div class="green">已完成</div>'
                                    default:
                                        return '<div >--</div>'
                                }
                            }
                        },
                        {title: '充电时长 (分钟)', field: 'useTime', textAlign: 'center', width: '130'},
                    ],
                    url: 'chargerScheme/shemeUseList',
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    isExport: true,
                    height: 300,
                    exportURL: 'chargerScheme/downShemeUseList'
                },
                tableProwOpt: {
                    column: [
                        {title: '序号', field: 'Index', textAlign: 'center', width: '40'},
                        {title: '方式', field: 'payCategory', textAlign: 'center', width: '100',type: 'string',
                            formatter: function (val) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch (vals) {
                                    case '1':
                                        return '<div >临时充值</div>'
                                    case '2':
                                        return '<div>包月充值</div>'
                                    case '3':
                                        return '<div>一次充满</div>'
                                    case '5':
                                        return '<div>IC卡充值</div>'
                                    default:
                                        return '<div >--</div>'
                                }

                            }},
                        {title: '时长', field: 'chargingTime', textAlign: 'center', width: '100'},
                        {title: '费用', field: 'money', textAlign: 'center', width: '50'},
                        {title: '状态', field: 'isEnable', textAlign: 'center', width: '130',type: "string",
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                if(vals ==='0'){
                                    return `<span class="red" style="color:#2F7FC4">停用</span>`;
                                }else{
                                    return `<span>启用</span>`;
                                }
                            }},
                        {title: '查看使用详情', field: 'useTime', textAlign: 'center', width: '130',type: "object",
                            formatter: function (val, index, row, obj) {
                                let template;
                                template = `<span class="list-operate"  @click="openAccount" style="color:#2F7FC4">查看使用详情</span>`;
                                return {
                                    template: template,
                                    methods: {
                                        openAccount: function () {
                                            obj.$parent.lookDetail(row);
                                        }
                                    }
                                }

                            }},
                    ],
                    data: [],
                    pageSize: 99999,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    noFooter: true,
                },
                tableIcOpt: {
                    url: 'chargerIC/queryCardSendList',
                    method: 'post',
                    data: [],
                    pageSize: 20,
                    pageNumber: 1,
                    height: 250,
                    pageList: [20, 40, 60],
                    queryParam: {},
                },
                ICqueryParam: {
                    cardId:'',
                    projectGuid:'',
                    deviceNo:''
                },
                modelDeviceParam:{
                    cardId: '',
                    projectGuid: '',
                    commNo: ''
                },
                tableICorPrice: {
                    url: '',
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    isExport: true,
                    exportURL: ''
                },
                colors: ['#B64439', '#1562AA', '#5EAEE3', '#FC6655', '#72B8BB', '#72A273', '#E97D53', '#8CC0A9', '#E59C87', '#74999F', '#DC6A65', '#FEDA5E', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'],
                pieData: [],
                power: {
                    minPower:"",
                    maxPower:""
                },
            }
        },
        components: {
            operateDia,
            addEditPlan,
            EquipMana,
            EquipManaInfo,
            ICCard,
            ICCardInfo
        },
        props: ['row', 'controls'],
        methods: {
            resetInfo: function () {
                this.Devicedetails = false
                let self = this;
                if(this.boxIndex == 1){
                    setTimeout(function () {
                        self.$refs.Mana.copyData();
                    },0)
                }else if(this.boxIndex == 2){
                    setTimeout(function () {
                        self.$refs.ICard.copyData();
                    }, 0)
                }
            },
            setInfo: function(val){
                this.Devicedetails = true;
                this.rowInfo = val
            },
            stopOrEnable: function (row) {
                let that = this;
                let onOrOff = row.onOrOff == 0?1:0;
                that.getRequest({
                    url: 'chargerScheme/offOnScheme',
                    method: 'post',
                    param: {
                        schemeGuids: [row.schemeGuid],
                        onOrOff: onOrOff
                    },
                    success: function (data) {
                        that.$refs.alerts.show({
                            msg: data.message
                        });
                        that.queryDetail();
                    },
                    error: function (data) {
                        that.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                })
            },
            formatAll: function () {
                let that = this;
                var webs = function () {
                    that.getWebsocket({
                        keyMsg: 'clearIcCard',
                        sign: true,
                        funMsg: function (data) {
                            that.$refs.loadings.close();
                            if(data.data.status == 0){
                                that.$refs.alerts.show({
                                    msg: '清除失败'
                                });
                            }else{
                                that.$refs.alerts.show({
                                    msg: '清除成功'
                                });
                            }
                            if(that.boxIndex == 2){
                                that.$refs.myTable.refreshTable(that.ICqueryParam, 1);
                            }else{
                                that.$refs.myTable.refreshTable(that.modelDeviceParam, 1);
                            }
                            that.$store.state.callback['clearIcCard'] = null;
                            that.$store.state.linkCheck = 'linkCheck';
                        }
                    })
                };
                var read = function () {
                    let param = [that.modelDeviceParam.commNo]
                    that.getRequest({
                        url: 'chargerIC/clearCard',
                        method: 'post',
                        param: param,
                        success: function (data) {
                            that.$refs.loadings.show({msg: '正在清除IC卡下发设备信息，请稍后。。。', hasClose: true});
                            webs();
                        },
                        error: function (data) {
                            that.$refs.alerts.show({
                                msg: data.message
                            });
                        }
                    })
                };
                read();
            },
            readIC: function () {
                let that = this;
                var webs = function () {
                    that.getWebsocket({
                        keyMsg: 'readIcCard',
                        sign: true,
                        funMsg: function (data) {
                            that.$refs.loadings.close();
                            if(data.data.status == 1){
                                that.$refs.alerts.show({msg: data.data.exist ? '档案在该设备已存在': '档案在该设备不存在'});
                            }else{
                                that.$refs.alerts.show({msg: '读取失败！请稍后重试'});
                            }
                            that.$store.state.callback['readIcCard'] = null;
                            that.$store.state.linkCheck = 'linkCheck';
                        }
                    })
                };
                var read = function () {
                    let param = [];
                    for (let i = 0; i < that.selected.length; i++){
                        param.push({
                            "cardId":that.selected[i].cardId,
                            "deviceNo":that.selected[i].deviceNo,
                            "commNo":that.selected[i].commNo,
                            "projectguid":that.projectguid,
                            "pointCode":that.selected[i].pointCode,
                            "state":that.selected[i].state,
                            "cardGuid": that.selected[i].cardGuid,
                        })
                    }
                    that.getRequest({
                        url: 'chargerIC/readCard',
                        method: 'post',
                        param: param,
                        success: function (data) {
                            that.$refs.loadings.show({msg: '正在读取IC档案，请稍后。。。', hasClose: true});
                            webs();
                        },
                        error: function (data) {
                            that.$refs.alerts.show({
                                msg: data.message
                            });
                        }
                    })
                }
                this.getConfirm({
                    emptyMsg: '请先选择要读取的设备!',
                    confirmMsg: '确定要对选中的设备进行读取操作吗？',
                    callback: {
                        confirm: function () {
                            console.log('-----')
                            read();
                        }
                    }
                });
            },
            sendIC: function () {
                let that = this;
                var webs = function () {
                    that.getWebsocket({
                        keyMsg: 'sendIcCard',
                        sign: true,
                        funMsg: function (data) {
                            that.$refs.loadings.close();
                            if(data.data.status == 0){
                                that.$refs.alerts.show({
                                    msg: '下发失败'
                                });
                            }else{
                                that.$refs.alerts.show({
                                    msg: '下发成功'
                                });
                            }
                            if(that.boxIndex == 2){
                                that.$refs.myTable.refreshTable(that.ICqueryParam, 1);
                            }else{
                                that.$refs.myTable.refreshTable(that.modelDeviceParam, 1);
                            }
                            that.$store.state.callback['sendIcCard'] = null;
                            that.$store.state.linkCheck = 'linkCheck';
                        }
                    })
                };
                var read = function () {
                    let param = [];
                    // for (let i = 0; i < that.selected.length; i++){
                    //     param.push({
                    //         "cardId":that.selected[i].cardId,
                    //         "deviceNo":that.selected[i].deviceNo,
                    //         "commNo":that.selected[i].commNo,
                    //         "projectguid":that.projectguid,
                    //         "pointCode":that.selected[i].pointCode,
                    //         "state":that.selected[i].state
                    //     })
                    // }
                    param = [{
                        "cardId":that.selected[0].cardId,
                        "deviceNo":that.selected[0].deviceNo,
                        "commNo":that.selected[0].commNo,
                        "projectguid":that.projectguid,
                        "pointCode":that.selected[0].pointCode,
                        "state":that.selected[0].state,
                        "cardGuid": that.selected[0].cardGuid,
                    }]
                    that.getRequest({
                        url: 'chargerIC/sendCard',
                        method: 'post',
                        param: param,
                        success: function (data) {
                            that.$refs.loadings.show({msg: '正在下发IC档案，请稍后。。。', hasClose: true});
                            webs();
                        },
                        error: function (data) {
                            that.$refs.alerts.show({
                                msg: data.message
                            });
                        }
                    })
                }
                this.getConfirm({
                    emptyMsg: '请先选择要下发的设备!',
                    confirmMsg: '确定要对选中的设备进行下发操作吗？',
                    callback: {
                        confirm: function () {
                            console.log('-----')
                            read();
                        }
                    }
                });
            },
            lowerHair: function (row, flag) {
                let self = this;
                if(flag === 1){
                    this.modelDeviceParam = {
                        commNo: row.commNo,
                        projectGuid: self.row.projectGuid
                    }
                    this.tableIcOpt.queryParam = this.modelDeviceParam
                }else if(flag === 2){
                    this.ICqueryParam = {
                        cardId: row.cardId,
                        projectGuid: self.row.projectGuid
                    }
                    this.tableIcOpt.queryParam = this.ICqueryParam
                }
                this.tableIcOpt.column = this.modelClum[flag];
                this.showIcDialog = true;
                setTimeout(function () {
                    self.$refs.addICPlan.show({
                        hasClose: true,
                        action:{
                            cancel: function () {
                                self.showIcDialog = false;
                            }
                        }
                    })
                }, 0)
            },
            changebox: function (row, index) {
                let self = this;
                this.boxIndex = index;
                if(index != 0){
                    let queryParam = null;
                    if(index == 1){
                        setTimeout(function () {
                            self.$refs.Mana.copyData();
                        },0)
                        queryParam = self.searchParam
                    }else{
                        queryParam = self.ICParam
                        setTimeout(function () {
                            self.$refs.ICard.copyData();
                        }, 0)
                    }
