                    param: {projectGuid:this.row.projectGuid},
                    success: function (data) {
                        console.log(data);
                        that.schemeList = data.data.sort(function(x, y){
                            return x.maxPower - y.maxPower;
                        });
                        if(data.data.length != 0){
                            that.index = 0;
                            that.$refs.myProwTable.resetTableOpt({data: that.schemeList[0].schemeList});
                            that.power.minPower = that.schemeList[0].minPower
                            that.power.maxPower = that.schemeList[0].maxPower
                            that.makePie(1);
                        }
                        that.showInfo = true;
