                    {title: '序号', field: 'Index', textAlign: 'center', width: '30'},
                    {title: '设备编号', field: 'deviceNo', textAlign: 'center', width: '100'},
                    {title: '通信编号', field: 'commNo', textAlign: 'center', width: '100'},
                    {title: '下发状态', field: 'sendFlag', textAlign: 'center', width: '50', type: 'string',formatter: function (val, index, row, obj) {
                            let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                            switch(vals) {
                                case '0':
                                    return '<div>未下发</div>'
                                case '1':
                                    return '<div class="green">已下发</div>'
                                default:
                                    return '<div >--</div>'
                            }
                        }},
                    {title: 'IC卡编号', field: 'cardId', textAlign: 'center', width: '50'},
                    {title: '下发时间', field: 'updateTime', textAlign: 'center', width: '100'},
                ];
                return ['',clum1, clum2]
            }
        },
        mounted: function(){
            this.$refs.myProwTable.onResize(false, this.$refs.myOwnProwTable);
        },
        created: function () {
            this.project[0] = this.row.projectName;
            this.project[1] = this.row.projectGuid;
            for (let key in this.row) {
                this.$set(this.commInfo, key, this.row[key])
            }
            this.tableShow = true;
            this.queryDetail();
        }
    }
</script>
<style lang="scss" >
$LInfoC: 16em;
.price-Info {
    .sColor0 .thead-div{
        background: rgba(80,158,213, 0.9);
    }
    .sColor1 .thead-div{
        background: rgba(81,201,194, .9);
    }
    .sColor2 .thead-div{
        background: rgba(229,163,104, .9);
    }
    .rInfoCon {
        width: calc(100% - #{$LInfoC} - 2em - 2px);
        float: left;
        //border-left:1px solid rgba(255,255,255,0.15)
    }
    .tabBox{
        width: 100%;
        overflow: hidden;
        height: 2.5em;
        line-height: 2.5em;
        li{
            float: left;
            /*border: 1px solid #3A5179;*/
            width: 8em;
            cursor: pointer;
            height: calc(100% - 2px);
        }
        .active{
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            border: 1px solid #3A5179;
            position: relative;
            /*background: #183566;*/
            border-bottom: 1px solid #173466;
            z-index: 2;
        }
        clear: both;
    }
    .list-box{
        width: 100%;
        height: calc(100% - 3em);
        overflow: hidden;
        border-top: 1px solid #3A5179;
        padding: .5em 5px 5px 5px;
        position: relative;
        top: -1px;
        display: flex;
        &>div{
            width: 50%;
        }
        .fanAnbox{
            width: 48%;
            margin-right: 2%;
        }
    }
    .list-box1{
        width: 100%;
        height: calc(100% - 2em);
        overflow: hidden;
        border-top: 1px solid #3A5179;
        padding: 5px;
        position: relative;
        top: -1px;
    }
    .lInfoCon {
        width: $LInfoC;
        float: left;
        .infoL {
            width: calc(100% - 5em);
            display: table;
            padding: 0.5em 2em 0em 3em;
            border-right: 1px solid rgba(255, 255, 255, 0.15);
            flex: 1;
            margin-top: 0.3em;
            min-height: 6em;
            .infoLCon {
                display: table-cell;
                margin: auto;
                vertical-align: middle;
                img {
                    width: 2.5em;
                    height: 2.8em;
                    float: left;
                    margin-top: 0.2em;
                    margin-right: 0.5em;
                }
                div {
                    float: left;
                    width: calc(100% - 3em);
                    height: 100%;
                }
                span {
                    display: block;
                    float: left;
                    width: 100%;
                    padding-left: 1em;
                    font-size: 0.83em;
                    text-align: left;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                span.hasColor {
                    color: #3E8EDE;
                    font-size: 1.8em;
                    font-weight: 600
                }
            }
        }
    }
    .price-chartCon {
        .selectedLi{

        }
        .simpleTopTab {
            border-bottom: 1px solid rgba(255, 255, 255, 0.15);
            font-size: 0.83em;
            li {
                padding: 0.6em 1.5em;
                float: left;
                cursor: pointer;
                color: white;
                background: rgba(#305ea0, 1);
                border-right: 1px solid rgba(255, 255, 255, 0.15);
                position: relative
            }

            li:last-child {
                border-right: 0
            }
            li:hover {
                background: rgba(255, 255, 255, 0.1);
            }
            li.selected {
                background: #227fdf;
            }
            li:hover:after, li.selected:after {
                content: '';
                position: absolute;
                width: 100%;
                height: 0.2em;
                background: #227fdf;
                bottom: 0;
                left: 0
            }
        }
        .simpleTopTab:after {
            clear: both;
            content: '';
            display: block;
            width: 1px;
            height: 0;
        }
        .lpiechart {
            height: 100%;
            position: relative;
            .pieLegendDiv {
                width: 100%;
                height: 2em;
                position: relative;
                .legendCon {
                    position: absolute;
                    padding: 0.9em 0 0 0;
                    top: 0%;
                    //left:50%;
                    width: 100%;
                    // transform: translate(0%, -50%);
                    .legendSpanCon {
                        position: relative;
                        margin: 0 0.5em;
                        min-width: 4.5em;
                        cursor: pointer;
                        display: inline-block;
                        span.legendSpan {
                            display: block;
                            font-size: 0.84em;
                            height: 100%;
                            text-align: left;
                            padding-left: 2.2em;
                            margin: 0 0.5em
                        }
                        span.legendSpanBefore {
                            display: block;
                            position: absolute;
                            height: 0.75em;
                            width: 1.6em;
                            border-radius: 0.2em;
                            top: 15%;
                            left: 0em;
                            background: rgba(255, 255, 255, 0.4);
                        }
                    }

                }
            }
            .piechart {
                width: 100%;
                height: calc(100% - 4em)
            }
        }
        .rtableDetail {
            width: calc((100% - 0em) * 0.5);
            height: calc(100% - 1.6em);
            margin: 0.8em 0 !important;
            font-size: 0.84em;
            p {
                text-align: left
            }
        }
    }
}
</style>



