<template>
    <div>
        <dialogs type="customer" ref="addPricePlan" v-if="showDialog" width="40em" :hasNoPaddB='true' overflow="auto">
            <div slot="header">方案配置</div>
            <div slot="content">
                <ul class="dialog-ul">
                    <li class="formList halfWidth">
                        <label class="label-show" style="min-width:4.8em;margin-left:0"><span class="emptyTips"> * </span>充满</label>
                        <selects  :option="priceType[4].option" @getSelect="getFrom"></selects>
                    </li>
                    <li class="formList halfWidth hasMarginLi">
                        <label class="label-show" style="min-width:4.8em;margin-left:0"><span class="emptyTips"> * </span>IC卡</label>
                        <selects  :option="priceType[3].option" @getSelect="getFrom"></selects>
                    </li>

                    <li class="formList halfWidth">
                        <label class="label-show" style="min-width:4.8em;margin-left:0"><span class="emptyTips"> * </span>月卡 </label>
                        <selects  :option="priceType[0].option" @getSelect="getFrom" ></selects>
                    </li>
                    <li class="formList halfWidth hasMarginLi">
                        <label class="label-show" style="min-width:0.8em;margin-left:0"><span class="emptyTips"> * </span>月卡次数</label>
                        <inputs  :option="priceType[2].option" @getInput="getFrom"></inputs>
                    </li>
                    <li class="formList halfWidth" style="width: calc(77% - 2em)!important;float: left">
                        <label class="label-show" style="min-width:0.8em;margin-left:0"><span class="emptyTips"> * </span>临充配置</label>
                        <selects  :option="priceType[1].option" @getSelect="getFrom"></selects>
                    </li>
                    <li class="formList halfWidth" style="width: calc(22% + 0.4em) !important;min-width: 4em!important;padding-right:1em;">
                        <button class="button-default dialogFromLB"  @click="addImmPrice" style="float: right;">
                            <i class="hasIcon " style="margin-top:4px;">
                                <icon name="plus-circle" scale="1.4"></icon>
                            </i>添加
                        </button>
                    </li>
                    <li class="allWidth" v-if="temporary.length > 0" style="padding-left:5.5em;width:calc(100% - 0.8em - 5.5em) !important">
                    <!--  <label class="label-show" style="margin-left: 0;"></label> -->
                        <span  class="formLiPriceSpan" v-for="(theItem, $index) in temporary" :key="$index" v-if="temporary.length > 0">
                                {{theItem}}小时
                        <span class="removePrice" @click="removeImmPrice(theItem, $index)"> × </span>
                        </span>
                    </li>
                </ul>
                <div style="margin:1em 0;text-align:left">注：双击进行编辑，月卡为一个月的资费，其他为当前小时资费，功率填当前档数最大值，其范围表示前面档数最大值到当前最大值</div>
                <div class="programme">
                    <ul>
                        <li>
                            <div style="border-left: 1px solid transparent;height: 3em;line-height: 3em;">功率/方式</div>
                            <div style="height: 3em;line-height: 3em;">一档</div>
                            <div style="height: 3em;line-height: 3em;">二挡</div>
                            <div style="height: 3em;line-height: 3em;">三挡</div>
                            <div style="height: 3em;line-height: 3em;">是否启用</div>
                        </li>
                    </ul>
                    <ul>
                        <li>
                            <label class="label-show" style="min-width:0.8em;margin-left:0;">功率</label>
                            <div  @click="editItem('sPower', -1)" class="sColor">
                                <inputs :option="programme.sPower" @getInput="getFrom" style="height: 3em;margin-top: 3px"></inputs>
                                <span v-if="programme.sPower.type ==='disabledInput'" class="prowSpan">
                                    0~{{Power.sPower}}
                                    <strong>W</strong>
                                </span>
                                <span>W</span>
                            </div>
                            <div  @click="editItem('mPower', -1)" v-if="programme.mPower" class="mColor">
                                <inputs :option="programme.mPower" @getInput="getFrom" style="height: 3em;margin-top: 3px"></inputs>
                                <span v-if="programme.mPower.type ==='disabledInput'" class="prowSpan">
                                    {{parseInt(Power.sPower)+1}}~{{Power.mPower}}
                                    <strong>W</strong>
                                </span>
                                <span>W</span>
                            </div>
                            <div v-else class="mColor">
                                <span @click="addPower(1)" style="cursor: pointer">点击增加功率</span>
                            </div>
                            <div  @click="editItem('lPower', -1)" v-if="programme.lPower" class="lColor">
                                <inputs :option="programme.lPower" @getInput="getFrom" style="height: 3em;margin-top: 3px"></inputs>
                                <span v-if="programme.lPower.type ==='disabledInput'" class="prowSpan">
                                    {{parseInt(Power.mPower)+1}}~{{Power.lPower}}
                                    <strong>W</strong>
                                </span>
                                <span>W</span>
                            </div>
                            <div v-else class="lColor">
                                <span @click="addPower(2)" style="cursor: pointer">点击增加功率</span>
                            </div>
                        </li>
                    </ul>
                    <ul v-for="(item, pindex) in programme.list" :key="pindex">
                        <li>
                            <label class="label-show" style="min-width:0.8em;margin-left:0;">{{item.title}}</label>
                            <div  @click="editItem('s', item.type)">
                                <inputs :option="item.s" @getInput="getFrom" style="height: 3em;margin-top: 3px"></inputs>
                                <span>¥</span>
                            </div>
                            <div @click="editItem('m', item.type)" v-if="item.m">
                                <inputs :option="item.m" @getInput="getFrom" style="height: 3em;margin-top: 3px"></inputs>
                                <span>¥</span>
                            </div>
                            <div v-else></div>
                            <div @click="editItem('l', item.type)" v-if="item.l">
                                <inputs :option="item.l" @getInput="getFrom" style="height: 3em;margin-top: 3px"></inputs>
                                <span>¥</span>
                            </div>
                            <div v-else></div>
                            <div>
                                <input type="checkbox"  class="checkBox dialogFromCheck"  style="float:left;left: 3.4em" @click="hasMonthPlan($event, pindex)" :checked="checkedArr[pindex]" />
                                <span class="inputChoose myCheckbox myCheckBox dialogFromCheckSpan"></span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div slot="footer">
                <button class="button-default" @click="submitData">提交</button>
                <button class="button-default cancel-button" @click="cancelDialog">取消</button>
            </div>
        </dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                checkedArr: [false, false, false],
                showDialog: false,
                temporary:[],
                oldTemporary: [],
                inputIndex:[],
                removeArr: [],
                immPrice: {
                    monthlyCnt: '',
                    chargingTime: '',
                    time: 4,
                    man: 4,
                    IC: 4
                },
                Power: {
                    "sPower": 500,
                    // "mPower": 500,
                    // "lPower": 800,
                },
                oldPower: {
                    "sPower": '',
                    "mPower": '',
                    "lPower": '',
                },
                powerShow:[],
                oldSchemeList: [],
                schemeList: {
                    sPower: {
                        man: '',
                        mon: '',
                        Ic: '',
                    },
                },
            }
        },
        props: ['priceData', 'operateFlag', 'projectGuid'],
        computed: {
            priceType: function () {
                let self = this;
                return [  
                    {    
                        type: 'select',
                        option:  {
                            type: 'select',
                            key: 'immPrice.time',
                            cs: 'client',
                            data: self.makeUpTimeSelect(25),
                            constant: {text: self.immPrice.time + '小时', value: self.immPrice.time},
                            width: 'calc((100% - 6.5em))',
                        }
                    },
                        {    
                        type: 'select',
                        option:  {
                            type: 'select',
                            key: 'immPrice.chargingTime',
                            cs: 'client',
                            data: self.makeUpTimeSelect(25),
                            width: 'calc((100% - 6.5em))',
                        }
                    },
                    {
                        type: 'input',
                        isEmpty: false,
                        option:  {
                           type: 'normalInput',
                            key: 'immPrice.monthlyCnt',
                            width: 'calc((100% - 6.5em))',  
                            placeHolder: '充电次数，1-40之间的整数', 
                            value: self.immPrice.monthlyCnt,
                            length: 2,
                            limit: 'number'
                        }
                    },{
                        type: 'select',
                        option:  {
                            type: 'select',
                            key: 'immPrice.IC',
                            cs: 'client',
                            data: self.makeUpTimeSelect(25),
                            constant: {text: self.immPrice.IC + '小时', value: self.immPrice.IC},
                            width: 'calc((100% - 6.5em))',
                        }
                    },{
                        type: 'select',
                        option:  {
                            type: 'select',
                            key: 'immPrice.man',
                            cs: 'client',
                            data: self.makeUpTimeSelect(25),
                            constant: {text: self.immPrice.man + '小时', value: self.immPrice.man},
                            width: 'calc((100% - 6.5em))',
                        }
                    },
                    
                ]
            },
            programme: function(){
                let self = this, m = false, l = false;
                if(!!this.Power.mPower){
                    m = true;
                }
                if(!!this.Power.lPower){
                    l = true
                }
                this.powerShow = [m, l];
                let obj = {
                    "sPower":self.setPowerOption(self.Power.sPower, '', 'Power.sPower', ''),
                    "mPower": m?self.setPowerOption(self.Power.mPower, '', 'Power.mPower', ''):false,
                    "lPower":l?self.setPowerOption(self.Power.lPower, '', 'Power.lPower', ''):false,
                    "list": [
                        {
                            title:'充满', 
                            s: self.setOption(self.schemeList.sPower.man, '' , 'schemeList.sPower.man'), 
                            m: m?self.setOption(self.schemeList.mPower.man, '' , 'schemeList.mPower.man'):false, 
                            l: l?self.setOption(self.schemeList.lPower.man, '' , 'schemeList.lPower.man'):false,
                            type:'man'
                            },
                        {
                            title:'月卡', 
                            s: self.setOption(self.schemeList.sPower.mon, '' , 'schemeList.sPower.mon'), 
                            m: m?self.setOption(self.schemeList.mPower.mon, '' , 'schemeList.mPower.mon'):false, 
                            l: l?self.setOption(self.schemeList.lPower.mon, '' , 'schemeList.lPower.mon'):false,
                            type: 'mon'
                            },
                        {
                            title:'IC卡', 
                            s: self.setOption(self.schemeList.sPower.Ic, '' , 'schemeList.sPower.Ic'), 
                            m: m?self.setOption(self.schemeList.mPower.Ic, '' , 'schemeList.mPower.Ic'):false, 
                            l: l?self.setOption(self.schemeList.lPower.Ic, '' , 'schemeList.lPower.Ic'):false,
                            type: 'Ic'
                            },
                    ]       
                }
                if(this.inputIndex[1] === -1){
                    for(let i in obj){
                        if(i === this.inputIndex[0]){
                            obj[i] = self.setPowerOption(self.Power[i], 'normal', 'Power.' + i)
                        }
                    }
                }
                for(let i = 0; i < this.temporary.length; i++) {
                    let t = 's' + this.temporary[i];
                    let item = {
                       title: '临充' + this.temporary[i] + '小时',
                       s: self.setOption(self.schemeList.sPower[t], '' , 'schemeList.sPower.'+t), 
                       m: m?self.setOption(self.schemeList.mPower[t], '' , 'schemeList.mPower.'+t):false, 
                       l: l?self.setOption(self.schemeList.lPower[t], '' , 'schemeList.lPower.'+t):false,
                       type: t
                    }
                    obj.list.push(item);
                }
                for (let i = 0; i < obj.list.length; i++){
                    if(obj.list[i].type == this.inputIndex[1]){
                        let t = obj.list[i].type;
                        let size = self.inputIndex[0] + 'Power'
                        obj.list[i][self.inputIndex[0]] = self.setOption(self.schemeList[size][t], 'normal', 'schemeList.'+size+'.'+t)
                    }
                }
                return obj;
            }
        },
        methods: {
            hasMonthPlan: function (event, index) {
                let checked = event.target.checked;
                this.checkedArr.splice(index, 1, checked);
            },
            addPower: function(val){
                if(val == 1){
                    this.$set(this.Power, 'mPower', parseInt(this.Power.sPower) + 100 < 1000 ? parseInt(this.Power.sPower) + 100: 999)
                    this.$set(this.schemeList, 'mPower', {
                        man: '',
                        mon: '',
                        Ic: '',
                    })
                    this.oldPower.mPower = this.Power.mPower
                }else if(val == 2){
                    if(this.Power.mPower === '' || typeof this.Power.mPower === 'undefined' || this.Power.mPower == null){
                        this.$refs.alerts.show({msg: '请先添加第二档功率'});
                        return;
                    }
                    this.$set(this.Power, 'lPower', parseInt(this.Power.mPower) + 100 < 1000 ? parseInt(this.Power.mPower) + 100: 999)
                    this.$set(this.schemeList, 'lPower', {
                        man: '',
                        mon: '',
                        Ic: '',
                    })
                    this.oldPower.lPower = this.Power.lPower
                }
            },
            editItem: function(size, index){
                this.inputIndex = [size, index];
            },
            setPowerOption: function(value, type, key){
                let obj = {
                    type: 'disabledInput',
                    // type: 'normalInput',
                    key: key,
                    width: 'calc(100% - 2em)',
                    placeHolder: '双击进行编辑', 
                    value: value,
                    length: 3,
                    cursor: 'text'
                }
                if(value === '双击进行编辑'){
                    obj.value = '双击进行编辑'
                    obj.limit = '';
                }else{
                    obj.limit = 'number';
                }
                if(type === 'normal'){
                    obj.type = 'normalInput';
                    if(value == '双击进行编辑'){
                        obj.value = ''
                    }
                }
                return obj;
            },
            setOption: function(value, type, key){
                let obj = {
                    type: 'disabledInput',
                    // type: 'normalInput',
                    key: key,
                    width: 'calc(100% - 2em)',
                    placeHolder: '双击进行编辑', 
                    value: value,
                    length: 6,
                    cursor: 'text',
                    regular: 'float7',
                    dataType: '+float',
                }
                if(value === '双击进行编辑'){
                    obj.value = '双击进行编辑'
                    obj.limit = '';
                }else{
                    obj.limit = 'float';
                }
                if(type === 'normal'){
                    if(value === '双击进行编辑'){
                        
                        obj.value = ''
                    }
                    obj.type = 'normalInput';
                }
                return obj;
            },
            addImmPrice: function () {
                if(this.temporary.indexOf(this.immPrice.chargingTime) != -1){
                    this.$refs.alerts.show({msg: '不能添加相同充电时长！'})
                    return;
                }
                this.schemeList.sPower['s' + this.immPrice.chargingTime] = '';
                for(let i in this.Power){
                    if(i == 'mPower'){
                       this.schemeList.mPower['s' + this.immPrice.chargingTime] = '';
                    }
                    if(i == 'lPower'){
                        this.schemeList.lPower['s' + this.immPrice.chargingTime] = '';
                    }
                }
                this.temporary.push(this.immPrice.chargingTime)
            },
            removeImmPrice: function(time, index){
                delete this.schemeList.sPower['s' + time]
                if(this.powerShow[0]){
                    delete this.schemeList.mPower['s' + time]
                }
                if(this.powerShow[1]){
                    delete this.schemeList.lPower['s' + time]
                }
                if(this.removeArr.indexOf(time) == -1){
                    this.removeArr.push(time);
                }
                this.temporary.splice(index, 1);
            },
            showOperate: function(){
                let self = this;
                this.copyData();
                this.showDialog = true;
                setTimeout(function () {
                    self.$refs.addPricePlan.show()
                }, 0)
            },
            makeUpTimeSelect: function (time) {
                let arr = [];
                for(let i =1; i< time; i++) {
                    arr.push({text: i + '小时', value: i})
                }
                return arr
            },
            getFrom: function (val) {
                let that = this;
                this.getOption(val, true);
                let key = val.key.split(".")
                if(key[1] == 'lPower' && key[0] == 'Power'){
                    if(val.val === ''){
                        that.$refs.confirms.show({
                            msg: `确定要删除该功率吗？`,
                            action: {
                                confirm: function () {
                                    // that.$delete(that.Power, 'lPower');
                                    that.$delete(that.schemeList, 'lPower')
                                    that.Power.lPower = null;
                                },
                                cancel: function () {
                                    that.Power.lPower = that.oldPower.lPower
                                }
                            }
                        });
                    }else if(parseInt(val.val) <= parseInt(that.Power.mPower)){
                        that.$refs.alerts.show({msg: '应该大于前面的功率'});
                        that.Power.lPower = that.oldPower.lPower
                    }
                    
                }
                if(key[1] == 'mPower' && key[0] == 'Power'){
                    if(val.val === ''){
                        that.$refs.confirms.show({
                        msg: `确定要删除该功率吗？`,
                        action: {
                            confirm: function () {
                                if(typeof that.Power.lPower != 'undefined'){
                                    that.Power.mPower = that.Power.lPower;
                                    that.schemeList.mPower = that.schemeList.lPower
                                    that.Power.lPower = null
                                    that.$delete(that.schemeList, 'lPower')
                                }else{
                                    that.Power.mPower = null
                                    that.$delete(that.schemeList, 'mPower')
                                }
                            },
                            cancel: function () {
                                that.Power.mPower = that.oldPower.mPower
                            }
                        }
                    });
                    }else if(parseInt(val.val) <= parseInt(that.Power.sPower)){
                        that.$refs.alerts.show({msg: '应该大于前面的功率'});
                        that.Power.mPower = that.oldPower.mPower
                    }else if(parseInt(val.val) >= parseInt(that.Power.lPower) && !!that.Power.lPower){
                        that.$refs.alerts.show({msg: '应该小于第三档功率'});
                        that.Power.mPower = that.oldPower.mPower
                    }
                }
                if(key[1] == 'sPower' && key[0] == 'Power'){
                    if(val.val === '' || val.val === 0){
                        that.$refs.alerts.show({msg: '一档功率不能为0和空'})
                        that.Power.sPower = that.oldPower.sPower
                    }
                }
                this.inputIndex = ['q', '-2']
            },
            copyData: function(){
                this.oldSchemeList = [];
                if(this.priceData.length == 0){
                    this.Power.sPower = 500;
                    return;
                }
                for(let i = 0; i < this.priceData.length; i++ ){
                    let t = this.setPower(i);
                    this.$set(this.schemeList, t, {
                        man: '',
                        mon: '',
                        Ic: '',
                    })
                    this.oldPower[t] = this.priceData[i].maxPower;
                    this.$set(this.Power, t, this.priceData[i].maxPower)
                    for(let j = 0; j < this.priceData[i].schemeList.length; j++){
                        this.oldSchemeList.push(this.priceData[i].schemeList[j])
                        let payCategory = this.priceData[i].schemeList[j].payCategory
                        let money = this.priceData[i].schemeList[j].money
                        let chargingTime = this.priceData[i].schemeList[j].chargingTime
                        let chargingCnt = this.priceData[i].schemeList[j].chargingCnt
                        let isEnable = true;
                        if(i === 0){
                            isEnable = this.priceData[0].schemeList[j].isEnable == 0?false:true
                        }
                        if(payCategory == 1){
                            if(i === 0){
                                this.checkedArr.push(isEnable);
                            }
                            if(this.temporary.indexOf(chargingTime) === -1){
                                this.temporary.push(chargingTime) 
                            }
                            this.oldTemporary.push({
                                time:chargingTime, minPower: this.priceData[i].minPower, 
                                maxPower: this.priceData[i].maxPower,
                                money: money,
                                schemeGuid: this.priceData[i].schemeList[j].schemeGuid
                            })
                            this.$set(this.schemeList[t], 's'+chargingTime, money);
                        }else if(payCategory == 2){
                            if(i === 0){
                                this.checkedArr[1] = isEnable;
                            }
                            this.schemeList[t].mon = money
                            this.immPrice.time = chargingTime;
                            this.immPrice.monthlyCnt = chargingCnt
                        } else if(payCategory == 3){
                            if(i === 0){
                                this.checkedArr[0] = isEnable;
                            }
                            this.schemeList[t].man = money
                            this.immPrice.man = chargingTime;
                        }else if(payCategory == 5){
                            if(i === 0){
                                this.checkedArr[2] = isEnable;
                            }
                            this.schemeList[t].Ic = money
                            this.immPrice.IC = chargingTime;
                        }
                    }
                }
            },
            setPower: function(num){
                switch(num){
                    case 0:
                        return 'sPower'
                    case 1:
                        return 'mPower'
                    case 2:
                        return 'lPower'
                }
            },
            submitData: function () {
                let that = this;
                let alerts = that.$refs.alerts;
                let obj = that.processData()
                var reg = /(^[1-9](\d+)?(\.\d{1,2})?$)|(^0$)|(^\d\.\d{1,2}$)/;
                if(that.immPrice.monthlyCnt == '' && this.checkedArr[1]){
                    alerts.show({msg: '请填写月卡次数'});
                    return;
                }
                for (let i in this.Power){
                    if(this.Power[i] >= 1000){
                        alerts.show({msg: '功率应当小于1000W'});
                    }
                }
                for (let i = 0; i < obj.length; i++){
                    let msg = this.setAlertMsg(obj[i])
                    if(obj[i].money === '' && obj[i].isEnable === 1){
                        alerts.show({msg: '价格不能为空'});
                        return;
                    }
                    if (reg.test(obj[i].money) != true && obj[i].isEnable === 1) {
                        alerts.show({msg: msg});
                        return;
                    }
                    if(parseFloat(obj[i].money) >= 100){
                        alerts.show({msg: msg});
                        return;
                    }
                    if(obj[i].isEnable === 0 && obj[i].flag === 0){
                        obj[i].flag = "";
                    }
                }
                // return
                this.$refs.loadings.show({msg: '正在提交数据，请稍后。。。', hasClose: true});
                that.$emit('priceData', obj);
            },
            setAlertMsg: function (obj) {

                let payCategory = '临时充值';
                if(obj.payCategory == 1){
                    payCategory = '临充' + obj.chargingTime + '小时'
                }else if(obj.payCategory == 2){
                    payCategory = '包月'
                }else if(obj.payCategory == 3){
                    payCategory = '一次充满'
                }else if(obj.payCategory == 5){
                    payCategory = 'IC卡'
                }
                let msg = `${obj.power + 1}档,${payCategory}价格填写不正确，应当小于100，且只能保留两位小数！`;
                return msg;
            },
            processData: function(){
                let self = this;
                let flag = 0, schemeList = [], power = 0,minPower = 0, maxPower = 0, result = [], h = 0;
                for (let i in this.schemeList){
                    let powerType = 0; 
                    if(i == 'sPower'){
                        minPower = power;
                        maxPower = this.Power.sPower
                        powerType = 0;
                    }else if(i == 'mPower'){
                        minPower = power;
                        maxPower = this.Power.mPower
                        powerType = 1;
                    }else{
                        minPower = power;
                        maxPower = this.Power.lPower
                        powerType = 2;
                    }
                    for (let j in this.schemeList[i]){
                        let payCategory = null;
                        let chargingTime
                        let chargingCnt = 0;
                        if(j == 'man'){
                            chargingTime = self.immPrice.man
                            payCategory = 3
                        }else if(j == 'mon'){
                            chargingTime = this.immPrice.time;
                            payCategory = 2
                            chargingCnt = self.immPrice.monthlyCnt;
                        }else if(j == 'Ic'){
                            chargingTime = self.immPrice.IC
                            payCategory = 5;
                        }else{
                            payCategory = 1
                            chargingTime = j.substr(1, j.length -1);
                        }
                        schemeList.push({
                            "payCategory":payCategory,
                            "money":this.schemeList[i][j],
                            "chargingTime":chargingTime,
                            "chargingCnt":chargingCnt,
                            "flag": 0,
                            "minPower":minPower,
                            "maxPower":maxPower,
                            "projectGuid": self.projectGuid,
                            "power": powerType,
                            "isEnable": self.checkedArr[h]?1:0
                        })
                        h++;
                    }
                    h = 0;
                    power = parseInt(this.Power[i]) + 1;
                }

                for(let i = 0; i < this.oldSchemeList.length; i++){
                    for (let j = 0; j < schemeList.length; j++){
                        if(this.oldSchemeList[i].power == schemeList[j].power && this.oldSchemeList[i].payCategory == schemeList[j].payCategory){
                            if(this.oldSchemeList[i].maxPower == schemeList[j].maxPower
                                && this.oldSchemeList[i].minPower == schemeList[j].minPower){
                                if(this.oldSchemeList[i].money == schemeList[j].money && this.oldSchemeList[i].chargingTime == schemeList[j].chargingTime){
                                    if(this.oldSchemeList[i].chargingCnt == schemeList[j].chargingCnt){
                                        if(this.oldSchemeList[i].isEnable != schemeList[j].isEnable){
                                            schemeList[j].flag = 3;
                                            schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                        }else{
                                            schemeList[j].flag = "";
                                            schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                        }
                                    }else{
                                        schemeList[j].flag = 2;
                                        schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                    }
                                }
                                if(this.oldSchemeList[i].chargingTime != schemeList[j].chargingTime && this.oldSchemeList[i].money == schemeList[j].money){
                                    schemeList[j].flag = 2
                                    schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                }
                                if(this.oldSchemeList[i].chargingTime == schemeList[j].chargingTime && this.oldSchemeList[i].money != schemeList[j].money){
                                    schemeList[j].flag = 2
                                    schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                }
                                if(this.oldSchemeList[i].chargingTime != schemeList[j].chargingTime && this.oldSchemeList[i].money != schemeList[j].money){
                                    if(schemeList[j].isEnable == 0){
                                        schemeList[j].flag = ""
                                    }else{
                                        schemeList[j].flag = 0
                                    }
                                    schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                }
                            }

                            if(this.oldSchemeList[i].maxPower != schemeList[j].maxPower
                                || this.oldSchemeList[i].minPower != schemeList[j].minPower){
                                schemeList[j].flag = 2;
                                schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                            }

                            if(this.oldSchemeList[i].money == schemeList[j].money && this.oldSchemeList[i].chargingTime == schemeList[j].chargingTime){
                                if(this.oldSchemeList[i].maxPower != schemeList[j].maxPower || this.oldSchemeList[i].minPower != schemeList[j].minPower){
                                    schemeList[j].flag = 2
                                    schemeList[j].schemeGuid = this.oldSchemeList[i].schemeGuid
                                }
                            }
                        }
                    }
                }

                for(let i = 0; i < this.oldSchemeList.length; i++){
                    let obj = this.oldSchemeList[i];
                    let schemeGuid = obj.schemeGuid;
                    let isExist = false;
                    for(let j = 0; j < schemeList.length; j++){
                        let aj = schemeList[j];
                        let n = aj.schemeGuid;
                        if(n == schemeGuid){
                            isExist = true;
                            break;
                        }
                    }
                    if(!isExist){
                        result.push(obj);
                    }
                }
                for(let i = 0; i < result.length; i++){
                    result[i].flag = 1
                    schemeList.push(result[i])
                }
                console.log(JSON.stringify((schemeList)))
                return schemeList;
            },
            cancelDialog: function(){
                this.$refs.loadings.close();
                this.showDialog = false;
                this.inputIndex = [];
                this.oldSchemeList = [];
                this.temporary = [];
                this.oldTemporary = [];
                this.Power = {
                    "sPower": 500
                }
                this.schemeList = {
                    sPower: {
                        man: '',
                        mon: '',
                        Ic: '',
                    },
                }
                this.oldPower = this.cleanSearchData(this.oldPower);
                this.immPrice = {
                    monthlyCnt: '',
                    chargingTime: '',
                    time: 4,
                    man: 4,
                    IC: 4
                };
                this.$refs.addPricePlan.close()
            }
        },
        created: function () {
        }
    }
</script>
<style lang="scss" scoped>
.programme{
    border: 1px solid #3A527B;
    border-bottom: none;
    ul{
        width: 100%;
        clear: both;
        overflow: hidden;
        height: 3em;
        border-bottom: 1px solid #3A527B;
        .sColor{
            background: rgba(80,158,213, 0.9);
            .prowSpan{
                background: rgba(80,158,213, 0.9);
                strong{
                    float: right;
                    margin-right: 9px;
                }
            }
        }
        .mColor{
            background: rgba(81,201,194, .9);
            .prowSpan{
                background: rgba(81,201,194, .9);
                strong{
                    float: right;
                    margin-right: 9px;
                }
            }
        }
        .lColor{
            background: rgba(229,163,104, .9);
            .prowSpan{
                background: rgba(229,163,104, .9);
                strong{
                    float: right;
                    margin-right: 9px;
                }
            }
        }
        li{
            clear: both;
            width: calc(100% - 1px);
            overflow: hidden;
            height: 3em;
            display: flex;
            // justify-content: space-between;
            label{
                text-align: center;
                float: left;
                width: 20%;
                padding: 0;
                margin-right: 0;
                height: 3em;
                overflow: hidden;
            }
            &>div{
                width: calc(20% - 1px);
                float: left;
                border-left: 1px solid #3A527B;
                overflow: hidden;
                position: relative;
                height: 100%;
                span{
                    height: 3em;
                    line-height: 3em;
                }
            }
            .prowSpan{
                position: absolute;
                text-indent: 14px;
                background: #041744;
                left: 0;
                width: 100%;
                text-align: left;
                height: 3em;
                line-height: 3em;
            }
        }
    }
}
</style>


