        changebox: function changebox(row, index) {
            var self = this;
            this.boxIndex = index;
            if (index != 0) {
                var queryParam = null;
                if (index == 1) {
                    setTimeout(function () {
                        self.$refs.Mana.copyData();
                    }, 0);
                    queryParam = self.searchParam;
                } else {
                    queryParam = self.ICParam;
                    setTimeout(function () {
                        self.$refs.ICard.copyData();
                    }, 0);
                }
                queryParam.projectGuid = self.row.projectGuid;
            } else {
                this.makePie(this.tabs[this.tabIndex].status);
            }
        },
        getFrom: function getFrom(val) {
            var key = val.key.split('.');
            if (val.combine) {
                if (this.searchParam[key[1]] !== val.val && key[0] === 'searchParam') {
                    this.searchParam = this.cleanSearchData(this.searchParam);
                    this.getOption(val, true);
                    this.changebox(this.tabbox[this.boxIndex], this.boxIndex);
                }
                if (this.ICParam[val.key.split('.')[1]] !== val.val && key[0] === 'ICParam') {
                    this.ICParam = this.cleanSearchData(this.ICParam);
                    this.getOption(val, true);
                    this.changebox(this.tabbox[this.boxIndex], this.boxIndex);
                }
                if (this.ICqueryParam[val.key.split('.')[1]] !== val.val && key[0] === 'ICqueryParam') {
                    this.getOption(val, true);
                    this.$refs.myTable.refreshTable(this.ICqueryParam, 1);
                }
                if (this.modelDeviceParam[val.key.split('.')[1]] !== val.val && key[0] === 'modelDeviceParam') {
                    this.getOption(val, true);
                    this.$refs.myTable.refreshTable(this.modelDeviceParam, 1);
                }
            } else {
                if (val.val != '') {
                    this.getOption(val, true);
                    this.changebox(this.tabbox[this.boxIndex], this.boxIndex);
                }
            }
        },
