        submitProject: function submitProject(param) {
            var that = this;
            delete param.monthly;
            delete param.temporary;
            param.projectGuid = that.row.projectGuid;
            that.$refs.loadings.show({ msg: '正在提交数据，请稍后。。。', hasClose: true });
            that.getRequest({
                url: 'chargerScheme/editProject',
                method: 'post',
                param: param,
                success: function success(data) {
                    that.$refs.loadings.close();
                    that.$refs.alerts.show({
                        msg: data.message
                    });
                    for (var key in param) {
                        that.$set(that.commInfo, key, param[key]);
                    }
                    this.projectName[0] = that.commInfo.projectName;
                },
                error: function error(data) {
                    that.$refs.loadings.close();
                    that.$refs.alerts.show({
                        msg: data.message
                    });
                }
            });
        },
        submitPlan: function submitPlan(param) {
            console.log(param);
            var that = this;
            var params = {
                schemeList: param
            };
            that.getRequest({
                url: 'chargerScheme/editScheme',
                method: 'post',
                param: params,
                success: function success(data) {
                    that.$refs.alerts.show({
                        msg: data.message
                    });
                    that.$refs.editPlan.cancelDialog();
                    that.queryDetail();
                },
                error: function error(data) {
                    that.$refs.alerts.show({
                        msg: data.message
                    });
                }
            });
        },
        queryDetail: function queryDetail() {
            var that = this;
            that.getRequest({
                url: 'chargerScheme/shemeList',
                method: 'post',
                param: { projectGuid: this.row.projectGuid },
                success: function success(data) {
                    console.log(data);
                    that.schemeList = data.data.sort(function (x, y) {
                        return x.maxPower - y.maxPower;
                    });
                    if (data.data.length != 0) {
                        that.index = 0;
                        that.$refs.myProwTable.resetTableOpt({ data: that.schemeList[0].schemeList });
                        that.power.minPower = that.schemeList[0].minPower;
                        that.power.maxPower = that.schemeList[0].maxPower;
                        that.makePie(1);
                    }
                    that.showInfo = true;
                },
                error: function error(data) {
                    that.$refs.alerts.show({
                        msg: data.message
                    });
                }
            });
        },
