<template>
    <div   class="info-div" style="height:calc(100% - 1.2em)">
        <div class="info-header" v-if="!price">
          <span class="callback" @click="resetComponent">IC卡充电管理 </span>
          <span> > IC卡使用详情</span>
        </div>
        <div class="info-container" style="height:calc(100% - 0em)">
            <div  class="list-content detail-content  list-contentB topInfoCons" >
               <div class="list-content commInfoRs">
                  <div class="list-header " style="border:0">
                    <i class="hasIcon hasImg">
                      <img src="dist/img/widsomApp/basic.png" />
                    </i>基本信息
                    <button class="list-operate" @click="editICCard" v-if="controls[2]">
                    <i class="hasIcon ">
                      <icon name="edit" ></icon>
                    </i>编辑
                 </button>
                  </div>
                  <ul class="form-list" >
                     <li v-for="(item,$index) in cardInfo" class="halfWidth" style="margin:0!important;min-width:auto">
                         <span class="label-span">{{item.text}}</span>
                         <span class="label-span" style="max-width: calc(100% - 8em);"> &nbsp;:&nbsp;&nbsp;{{item.value}}</span>
                     </li>
                  </ul>
              </div>
              <div class="list-content cardInfoLs">
                 <div class="list-header " style="border:0">
                   <i class="hasIcon hasImg">
                     <img src="dist/img/widsomApp/data_statistics.png" />
                   </i>数据信息
                 </div>
                 <div class="accountCon">
                   <div class="oneInFours">
                     <div class="contentES ">
                       <div class="analyseTypeS">
                          <div>状态</div>
                          <div class=" bigSp" >{{getEnumItem('cardStatus', row.cardState).text}}</div>
                       </div>
                     </div>
                   </div>
                   <div class="oneInFours">
                      <div class="contentES BO">
                         <div  class="analyseTypeS">
                            <div>余额</div>
                            <div class=" bigSp">{{row.icRemainAmount}}</div>
                            <div>元</div>
                         </div>
                      </div>
                   </div>
                   <div class="oneInFours">
                      <div class="contentES BY">
                        <div  class="analyseTypeS">
                           <div>使用</div>
                           <div class=" bigSp" >{{row.useCnt}}</div>
                            <div>次</div>
                        </div>
                      </div>
                   </div>
                   <div class="oneInFours">
                      <div class="contentES BG">
                         <div  class="analyseTypeS">
                            <div>充值</div>
                            <div class=" bigSp" >{{row.payCnt}}</div>
                            <div>次</div>
                          </div>
                       </div>
                   </div>
                 </div>
              </div>
            </div>
            <div  class="list-content  list-contentB " style="padding: 0.5em 1.5em;height:calc(100% - 15.5em)" >
            <!--   <div class="list-header noborder-ListHeader" >
                 <i class="hasIcon hasImg">
                  <img src="dist/img/widsomApp/table_list.png" />
                </i>IC卡记录信息
              </div> -->
              <div class="operate-div">
                  <datePicker :dateOpt="searchDateOpt" @getDatePicker="getFrom" type='doubleDate'></datePicker>
              </div>
              <div class="tabs-show tab-div" style="margin:0.8em 2em 1em 0em !important">
                <ul>
                 <li v-for="(tabs, $index) in btnOpt" :class="{'selectedLi':  $index == btnIndex}"  style="min-width: 6em; font-size:0.94em;padding: 0.72em 1em"
                    @click="getTab(tabs, $index)">
                     {{tabs.name}}
                 </li>
                </ul>
              </div>
              <div ref="myOwnTable" class="table-container  " style="padding:0;width:calc(100%);height:calc(100% - 4em)" >
                 <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding smallPadding" v-if="tableShow" @rowDbClick=" openDialogs"></tables>
              </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
        <dialogs type="customer" ref="detail" width="65em" maxHeight="620px">
            <div slot="header">
                <span>{{title}}</span>
            </div>
            <div slot="content">
                <div class="costAccount-content">
                    <div class="mychar-box">
                        <div ref="currentChart" class="mychar"></div>
                        <div ref="voltageChart"></div>
                        <div ref="powerChart"></div>
                    </div>
                    <tables ref="caution"  class="dialogTable" :tableOpt="dialogTable"  v-if="isDialogTable"></tables>
                </div>
            </div>
        </dialogs>
        <dialogs type="customer" ref="editIC"  width="45em" :hasNoPaddB='true'>
           <div slot="header">
              编辑IC卡
           </div>
           <div slot="content" >
             <ul class="dialog-ul">
               <li v-for="(item,$index) in editList" class=" formList " :class="item.classes">
                  <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
                  <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
                  <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
                  <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
                  <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
               </li>
            </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitEdit">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
    </div>
</template>
<script>
    export default{
        data() {
            return {
               tableShow: false,
               btnOpt: [
                    {name: '充值记录', urls: 'dist/json/ICCardPay.json', url: 'chargerIC/queryPayList', download: 'chargerIC/downloadPayList'},
                    {name: '使用记录', urls: 'dist/json/deviceUse.json', url: 'chargerIC/queryUseListByIC', download: 'chargerIC/downloadUseList'}
               ],
               btnIndex: 0, 
               searchParam: {
                  cardId: '',
                  startDate: '',
                  endDate: ''
               },
               searchDate: '',
             
               tableOpt: {
                 column:[],
                  method: 'post',
                // method: 'get',
                 pageSize: 20,
                 pageNumber: 1,
                 pageList: [20, 40, 60],
                 queryParam: {},
                 isExport: true,
                   // height: 288,
                 exportURL: 'charger/deviceDetialUseDown'
              },
                // 以下为弹框
                lineData: {},
                deviceCommInfo: "",
                lineV: '',
                lineA: '',
                lineR: '',
                isDialogTable: false,
                title: '',
                dialogTable:{
                    column: [
                        {title: '时间', width: '140', field: 'heartTime', textAlign: 'center'},
                        // {title: '端口', width: '120', field: 'port', textAlign: 'center'},
                        {title: '状态', field: 'state', textAlign: 'center',  width: 120,type: 'string',tips: 'eventContent',
                          formatter: function (val, index, row, obj) {
                           let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                              switch(vals) {
                                  case '0':
                                      return `<div class="green">正常</div>`
                                  case '1':
                                  case '-2':
                                      return `<div class="red">告警</div>`
                                  case '-1':
                                      return `<div class="orange">充电结束</div>`
                                   default: 
                                      return `<div>--</div>`   
                                  }
                            
                          }
                        },
                        {title: '电压', width: '120', field: 'voltage', textAlign: 'center'},
                        {title: '电流', width: '120', field: 'current', textAlign: 'center'},
                        {title: '功率', width: '120', field: 'power', textAlign: 'center'},
                        {title: '电量', width: '120', field: 'activeTotal', textAlign: 'center'},
                        {title: '剩余时间(分钟)', width: '160', field: 'remainTime', textAlign: 'center',type: 'string',formatter: function (val, index, row, obj) {
                                if(typeof val !== 'undefined') {
                                    return val.toString()
                                }
                                return '--'
                            }
                        }
                    ],
                    method: 'post',
                    url: 'charger/queryHeartBychargingGuid',
                    // noFooter: true,
                    queryParam: {},
                    pageSize: 20,
                    pageNumber: 1,
                    height: 250,
                    isExport: true,
                    exportURL: 'charger/downHeartBychargingGuid',
                },
                formData: {
                    remark: '',
                    cardId: '',
                    customerName: '',
                    initAmount: '',
                    projectGuid: '',
                    customerContact: '',
                    cardGuid: ''
                }
            }
        },
        props: ['row', 'controls', 'searchParams','price'],
        methods: {
            submitEdit: function () {
                let that = this;
                if(that.formData.projectGuid == '') {
                      that.$refs.alerts.show({
                          msg: '所属项目不能为空！'
                       });
                      return 
                }
                if (this.formData.cardId.length < 8) {
                    this.formData.cardId = this.padLeft(this.formData.cardId, 8);
                }
                that.regularTest({
                   emptyKey: ['customerContact'],
                   prefix: 'formData',
                   callback: function () {
                      that.$refs.loadings.show({msg: '正在提交数据，请稍后。。。', hasClose: true});
                      that.getRequest({
                        url: 'chargerIC/edit',
                        method: 'post',
                        param: that.formData,
                        success: function (data) {
                          that.$refs.loadings.close();
                          that.$refs.alerts.show({
                             msg: data.message
                          });

                         that.cancelDialog();
                         //that.resetComponent();
                        },
                       error: function (data) {
                         that.$refs.loadings.close();
                         for(let key in that.row) {
                           that.formData[key] =  that.row[key];
                          }
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                       }
                    })
                 }
              })
            },
            cancelDialog: function () {
              this.$refs.editIC.close();
              for(let key in this.formData) {
                this.formData[key] = this.row[key] || ''
              }
              this.formData.initAmount = this.row.initAmount
            },
            editICCard: function () {
              this.$refs.editIC.show();
            },
            getTab: function(tab, index) {
              this.btnIndex = index;
              this.$refs.myTable.resetTableOpt({
                 column: this.columns[this.btnIndex],
                 url: this.btnOpt[this.btnIndex]['url'],
                 exportURL: this.btnOpt[this.btnIndex]['download']
              })
            },
            getFrom: function (val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['cardId', 'startDate', 'endDate']);
                this.getOption(val, true);
            },
            getParams: function (pageNumber) {
               let arr = this.getSearchData(this.searchParam);
               this.getHeight('calc(100% - 3.5em)');
               this.searchTable(arr, pageNumber);
            },
           //返回列表
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
           
            //电压折线图
            getLineC: function () {
                this.lineV  = echarts.init(this.$refs.currentChart);
                this.getLine(this.lineV, ['rgba(22,90,162, 1)', 'rgba(22,90,162, 0.1)'], ['电压', 'V'], 'yVoltageData', 'popup_blue')
            },
            //电流折线图
            getLineV: function () {
                this.lineA  = echarts.init(this.$refs.voltageChart);
                this.getLine(this.lineA, ['rgba(192,64,89, 1)', 'rgba(192,64,89, 0.1)'], ['电流', 'A'], 'yCurrentData', 'popup_red')
            },
            //功率折线图
            getLineR: function () {
                this.lineR = echarts.init(this.$refs.powerChart);
                this.getLine(this.lineR,  ['rgba(184,193,62, 1)', 'rgba(184,193,62, 0.1)'], ['功率', 'W'], 'yPowerData', 'popup_yellow')
            },
            getLine: function (obj, color, typeText, key, img) {
                let option = {
                    toolbox: {
                        show: false,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    dataZoom: [
                        {
                            type: 'slider',
                            show: true,
                            xAxisIndex: [0],
                            start: 1,
                            end: 100
                        },
                        {
                            type: 'inside',
                            xAxisIndex: [0],
                            start: 0,
                            end: 100
                        }
                    ],
                    grid: {
                        left: 40,
                        right: 30,
                        bottom:40,
                        top: 50
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['1月' ,'2月', '3月', '4月', '5月', '6月', '7月','8月', '9月', '10月','11月','12月'],
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 0.1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color:'rgba(255,255,255, 1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                type: 'dotted',
                                width: 2
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'

                            }
                        }
                    },
                    yAxis: {
                        name: typeText[0] + ' ( ' + typeText[1] + ' ) ',
                        type: 'value',
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color: 'rgba(255,255,255, 1)'
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                width: 2,
                                type: 'dotted'
                            }
                        }
                    },
                    tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    series:
                        {
                            name: typeText[0],
                            type:'line',
                            smooth:false,
                            symbol: "circle",
                            symbolSize: 8,
                           // sampling: 'max',
                            itemStyle: {
                                normal: {
                                    color: color[0]
                                }
                            },
                            markPoint: {
                                // symbol: "pin",
                                data: [
                                    {
                                        type: 'max',
                                        name: '最大值',
                                        symbol: 'image://dist/img/widsomApp/'+img+'.png',
                                        symbolOffset: [0, '-55%'],
                                        label: {
                                            normal: {
                                                padding: [5,10,15,10],
                                                color: '#fff',
                                                position: 'inside'
                                            }

                                        }
                                    }
                                ]
                            },
                            areaStyle: {
                                normal: {
                                    color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: color[0]
                                    }, {
                                        offset: 1,
                                        color: color[1]
                                    }])
                                }
                            },
                            data: []
                        }
                };
                option.xAxis.data  = this.lineData.xData;
                option.series.data  = this.lineData[key];
                obj.clear();
                obj.setOption(option, true);
            },
            getLineData: function (row) {
                let that = this;
                //'charger/deviceCurve'
                that.getRequest({
                    // url: 'charger/queryCurve',
                    url: 'charger/queryCurveCount',
                    method: 'post',
                    param: {
                        chargingPlieGuid: row.chargingPlieGuid,
                        chargingGuid: row.chargingGuid
                    },
                    success: function (data) {
                        let Data = data.data;
                        that.lineData.xData = Data.xData || [];
                        that.lineData.yPowerData = Data.yPowerData || [];
                        that.lineData.yCurrentData = Data.yCurrentData || [];
                        that.lineData.yVoltageData = Data.yVoltageData || [];
                        that.getLineC();
                        that.getLineV();
                        that.getLineR();
                    }
                }, true)
            },
            openDialogs: function (row) {
               if(this.btnIndex != 1) {
                 return
               }
                let that = this;
                this.title = '设备编号:' + row.val.deviceNo;
                this.$refs.detail.show({
                    hasClose: true,
                    action:{
                        cancel: function () {
                            that.isDialogTable = false;
                        }
                    }
                });
                this.dialogTable.queryParam.chargingPlieGuid = row.val.chargingPlieGuid;
                this.dialogTable.queryParam.chargingGuid = row.val.chargingGuid;
                this.isDialogTable = true;
                this.getLineData(row.val);
            },
         
        },
        computed: {
          searchDateOpt()  {
            let dates = this.initDate();
             return {
                   key: 'searchDate',
                   align: 'right',
                   width: '23em',
                   fontSize: '0.85em',
                   value: {startDate: dates.start, endDate: dates.end}
             }     
          },
          editList() {
            let width = 'calc(100% - 6.5em)';
            let obj = [{
                classes: 'halfWidth',
                type: 'input',
                isEmpty: false,
                label: '卡号',
                options: {
                    type: 'disabledInput',
                    width: width,
                    limit: 'number',
                    value: this.formData.cardId,
                    length: 8,
                    key: 'formData.cardId'
                }
            },
                {
                    classes: 'halfWidth hasMarginLi',
                    type: 'input',
                    isEmpty: false,
                    label: 'IC卡UID',
                    options: {
                        key: 'formData.cardGuid',
                        width: width,
                        type: 'disabledInput',
                        length: 16,
                        value: this.formData.cardGuid,
                        placeHolder: '1-16位数字和字母（a-f、A-F）组合',
                        errorMsg: '1-16位数字和字母（a-f、A-F）组合',
                        limit: 'moteEUI',
                        regular:  'otherDeviceNo' ,
                    }
                },
                {
                    classes: 'halfWidth',
                    type: 'input',
                    isEmpty: false,
                    label: '初始金额',
                    options: {
                        type: 'disabledInput',
                        width: width,
                        placeHolder: '0-500之间的任意整数',
                        length: 3,
                        key: 'formData.initAmount',
                        value: this.formData.initAmount
                    }
                },
                {
                    classes: 'halfWidth hasMarginLi',
                    type: 'input',
                    isEmpty: true,
                    label: '姓名',
                    options: {
                        type: 'normalInput',
                        width: width,
                        placeHolder: '1-20个字符',
                        value: this.formData.customerName,
                        length: 20,
                        key: 'formData.customerName'
                    }
                }]
              let classes = 'halfWidth hasMarginLi'
              if(!this.price){
                obj.push({
                    type: 'select',
                    classes: 'halfWidth',
                    label: '所属项目',
                    isEmpty: false,
                    options: {
                        type: 'complexSelect',
                        key: 'formData.projectGuid',
                        cs: 'server',
                        url: 'charger/queryProject',
                        method: 'post',
                        noCheck: {key: 'leaf', value: 0},
                        constant:!!this.formData.projectGuid ? {projectGuid: this.formData.projectGuid } : '',
                        width: width,
                        text: 'projectName',
                        value: 'projectGuid'
                    }
                })
              }else{
                  classes = 'allWidth'
              }
              obj.push({
                  classes: classes,
                  type: 'input',
                  isEmpty: false,
                  label: '电话',
                  options: {
                      key: 'formData.customerContact',
                      value: this.formData.customerContact,
                      width: width,
                      type: 'normalInput',
                      length: 11,
                      regular: 'phone',
                      errorMsg: '电话：11位手机号',
                      limit: 'number'
                  }
              })
              obj.push({
                  classes: 'allWidth',
                  type: 'input',
                  isEmpty: true,
                  label: '备注信息',
                  options: {
                      type: 'normalInput',
                      value: this.formData.remark,
                      width: width,
                      placeHolder: '50个字符内',
                      key: 'formData.remark'
                  }
              })
            return obj;
          },
          cardInfo(){
             return [
                {text: '卡号', value: this.row.cardId || '--'},
                {text: '创建时间', value: this.row.createTime || '--'},
                {text: '姓名', value: this.formData.customerName || '--'},
                {text: '更新时间', value: this.row.updateTime || '--'},
                {text: '电话', value: this.formData.customerContact || '--'},
                {text: '绑定账户', value: this.row.customerType || '--'},
                {text: '微信ID', value: this.row.webcharNo || '--'},
                {text: '支付宝ID', value: this.row.alipayUserId || '--'},
             ]
          },
          columns() {
            return [
             [
              {title: '序号', field: '', textAlign: 'center', width: '100', type: 'string', formatter: function (val, index, row, obj, indexs, startNum) {
                  return indexs + startNum;
                }
              },
              {title: '充值金额 (元)', field: 'payMoney',   textAlign: 'center', width: '140'},
              {title: '账户余额 (元)', field: 'iCRemainAmount',   textAlign: 'center', width: '140'},
              {title: '充值方式', field: 'payWay',    textAlign: 'center', width: '120', type: 'string', formatter: function (val, index) {
                   switch(val) {
                     case 1:
                      return '<div>微信</div>'
                     case 2:
                      return '<div>支付宝</div>'
                     case 3:
                      return '<div>现金</div>'
                   }
                }},
              {title: '操作人员', field: 'payUser', textAlign: 'center', width: '160'},
              {title: '充值时间', field: 'createTime',    textAlign: 'center', width: '160'}
             ],
             [
              {title: '序号', field: '', textAlign: 'center', width: '100', type: 'string', formatter: function (val, index, row, obj, indexs, startNum) {
                  return indexs + startNum;
                }
              },
              {title: '设备编号', field: 'deviceNo',   textAlign: 'center', width: '140'},
              {title: '时长 (分钟)', field: 'chargingTime',   textAlign: 'center', width: '140'},
              {title: '开始时间', field: 'startTime', textAlign: 'center', width: '160'},
              {title: '结束时间', field: 'endTime', textAlign: 'center', width: '160'},
              {title: '扣费金额(元)', field: 'deductMoney', textAlign: 'center', width: '100'},
              {title: '余额 (元)', field: 'remainAmount',   textAlign: 'center', width: '120'},
              {title: '地点', field: 'installAddr',    textAlign: 'center', width: '160'}
              ]
            ]
          }
        },
        created: function () {
          for(let key in this.formData) {
             this.formData[key] = this.row[key]
          }
          let dates = this.initDate();
          let self = this;
          this.tableOpt.queryParam.cardId =  this.searchParam.cardId =  this.row.cardId;
          this.tableOpt.queryParam.startDate =this.searchParam.startDate =  dates.start;
          this.tableOpt.queryParam.endDate = this.searchParam.endDate = dates.end;
          this.tableOpt.url = this.btnOpt[this.btnIndex]['url'];
          this.tableOpt.exportURL = this.btnOpt[this.btnIndex]['download']
          this.tableOpt.column = this.columns[this.btnIndex];
          this.searchDate = dates.startDate + '至' + dates.endDate;
        },
        mounted: function () {

        },
        watch: {
            'searchDate': {
                handler(val, oldVal) {
                    if (val !== '' && val !== oldVal) {
                        let arr = val.split('至');
                        this.searchParam.startDate = arr[0];
                        this.searchParam.endDate = arr[1];
                        if(this.tableShow) {
                           this.getParams(1)
                         } else {
                           this.tableShow = true
                         }
                    }
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
  @import "../../../assets/main.scss";
  $commInfoHs: 12em;
  .topInfoCons{
    padding:0 1em !important;
    height:commInfoHs;
    .list-content{
      padding:0.3em 0.5em !important;
    }
    display: flex;
    .cardInfoLs{
      flex: 2;
      position:relative;
      height:100%;
      padding-left:2em!important;
      margin-left:2em;
      margin-top:0!important;
      .accountCon{
        height:calc(#{$commInfoHs} - 2em);
        display:flex;
        .oneInFours{
           /*flex: 1;*/
           width: 25%;
           height:calc(100% - 2em);
           float:left;
           margin: 2em 0.5em 0 0.5em;
           div.contentES{
             display:table;
             width:100%;
             background: url('../../../../dist/img/widsomApp/state.png') no-repeat;
             height: 4.75em;
               background-position: left;
             /*background-position: center;*/
             /*background-size: contain;*/
             background-size: 100% 100%;
             width: 17.5em;
             div.analyseTypeS{
               padding-left:30%;
               display: table-cell;
               vertical-align: middle;
               text-align: left;
                width: -webkit-fit-content;
                width: -moz-fit-content;
                width: fit-content;
               div{
                   display: table-cell;
                   vertical-align: middle;
                   height: 100%;
                  // float: left;
                  font-size:0.87em;
                  //margin-top: 6%;
               }
               div.bigSp{
                 font-size: 2em;
                 padding:0 0.1em 0 0.4em;
               }
               div.bigSp+div{
                 font-size: 0.8em;
                 padding-top:12%
               }
             }
          }
          div.BY{
            background-image: url('../../../../dist/img/widsomApp/history.png');
          }
          div.BO{
            background-image: url('../../../../dist/img/widsomApp/money.png');
          }
          div.BG{
            background-image: url('../../../../dist/img/widsomApp/charging.png');
          }
        }
      }
    }
    .cardInfoLs:before{
      content: '';
      position: absolute;
      height:80%;
      background: rgba(255,255,255, 0.15);
      /*width: 1px;*/
      left:0;
      top:10%;
      outline: 1px solid rgba(255,255,255,0.15);
    }
    .commInfoLs:before{
        content: '';
        position:absolute;
        display:block;
        height:76%;
        left: 0;
        top: 12%;
        width:1px;
        background: rgba(255,255,255,0.3);
    }
    .commInfoLs:after{
        @extend .clearElement
    }
    .commInfoRs{
        flex: 1;
        font-size:1.05em;
        height: $commInfoHs;
        .form-list{
           margin:1em 0.5em;
        }
      }
    }

    .bottomList{
      height:calc(100% - 14em);
      .chartCon{
          display:flex;
          justify-content: space-around;
          height:calc(100% - 3em);
         .oneInThree{
             flex: 1;
             height:100%;
             margin:0 0.8em;
             .chart{
                height: calc(100% - 3em);

                width:100%
             }
             p{
               font-size: 0.9em;
               width: -webkit-fit-content;
               width: -moz-fit-content;
               width: fit-content;
               margin: auto;
             }
          }
      }

    }
  .costAccount-content{
      width: 100%;
      .mychar-box{
          width: 100%;
          height: 15em;
          display: flex;
          &>div{
              height: 15em;
              width: 33%;
          }
      }
  }
</style>



